<template>
  <v-container >
    <v-row>
      <v-col
        cols="12"
        md="6"
        sm="12"
      >
        <v-card class="pl-3 pr-3 pt-2 pb-3 elevation-6" >
          <v-form
            v-model="form.valid"
            ref="form"
          >
            <v-container>
              <v-row>
                <v-col
                  cols="12"
                  xs="12"
                >
                  <v-text-field
                    v-model="form.input.current_password"
                    :append-icon="form.show1 ? 'fa-eye-slash' : 'fa-eye'"
                    :rules="[form.rules.required]"
                    :type="form.show1 ? 'text' : 'password'"
                    name="Password Lama"
                    label="Password Lama"
                    class="required"
                    autocomplete="current-password"
                    counter
                    @click:append="form.show1 = !form.show1"
                  />
                </v-col>
              </v-row>
              <v-row>
                <v-col
                  cols="12"
                  xs="12"
                >
                  <v-text-field
                    v-model="form.input.new_password"
                    :append-icon="form.show2 ? 'fa-eye-slash' : 'fa-eye'"
                    :rules="[form.rules.required]"
                    :type="form.show2 ? 'text' : 'password'"
                    name="Password Baru"
                    label="Password Baru"
                    class="required"
                    autocomplete="new-password"
                    counter
                    @click:append="form.show2 = !form.show2"
                  />
                </v-col>
              </v-row>
              <v-row>
                <v-col
                  cols="12"
                  xs="12"
                >
                  <v-text-field
                    v-model="form.input.confirm_password"
                    :append-icon="form.show3 ? 'fa-eye-slash' : 'fa-eye'"
                    :rules="[form.rules.required]"
                    :type="form.show3 ? 'text' : 'password'"
                    name="Konfirmasi Password Baru"
                    label="Konfirmasi Password Baru"
                    class="required"
                    autocomplete="new-password"
                    counter
                    @click:append="form.show3 = !form.show3"
                  />
                </v-col>
              </v-row>
              <v-row justify="end">
                <v-btn
                  v-show="!form.loading"
                  type="button"
                  class="primary mt-5"
                  large
                  @click="changePassword"
                >Simpan</v-btn>
                <v-progress-circular
                  v-show="form.loading"
                  indeterminate
                  color="primary"
                />
              </v-row>
            </v-container>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        form:{
          input: {
            current_password: '',
            new_password: '',
            confirm_password: ''
          },
          loading: false,
          rules: {
            required: v => !!v || 'Harus diisi'
          },
          show1: false,
          show2: false,
          show3: false,
          valid: true
        }
      }
    },

    methods: {
      changePassword () {
        const _this = this
        
        if (_this.form.input.new_password != _this.form.input.confirm_password){
          _this.alert("Password baru tidak sama")
        } else if (_this.$refs.form.validate()) {
          _this.form.loading = true

          _this.$http.post('/admin/change-password', _this.form.input)
            .then((/* response */) => {
              _this.alert('Berhasil memperbaharui password');
              _this.$refs.form.resetValidation()
              _this.resetForm();
            })
            .finally(() => {
              _this.form.loading = false
            });
        } else {
          _this.alert("Harap isi form")
        }
      },

      resetForm () {
        this.form.input.current_password = ''
        this.form.input.new_password = ''
        this.form.input.confirm_password = ''
      }
    },

    metaInfo: {
      title: 'Ganti Password',
    }
  }
</script>