<template lang="html">
  <v-app id="login_form">
    <v-container>
      <v-layout align-center justify-center>
        <v-flex xs10 md8 lg6>
          <v-card class="pl-3 pr-3 pt-2 pb-3 mt-4 elevation-6" >
            <v-container class="py-2">
              <v-layout align-center justify-center>
                <img style="width:calc(150px + 4vw)"
                  src="../assets/logo.png"
                >
              </v-layout>
            </v-container>

            <div class="text-center mb-3 font-weight-thin" style="font-size:calc(18px + 1vw)">{{ $appname }}</div>

            <v-form xs10 @submit.prevent="signIn" v-show="(!is_loading)" >
              <v-text-field
                label="Username"
                v-model="username"
              />
              
              <v-text-field
                v-model="password"
                :append-icon="show1 ? 'fa-eye-slash' : 'fa-eye'"
                :rules="[rules.required]"
                :type="show1 ? 'text' : 'password'"
                name="password"
                label="Password"
                counter
                @click:append="show1 = !show1"
              />

              <v-layout class='mt-3' justify-end>
                <v-flex shrink>
                  <v-btn type=submit class="primary" large >Masuk</v-btn>
                </v-flex>
                
              </v-layout>
            </v-form>

            <v-layout justify-center v-show="is_loading">
              <v-progress-circular
                indeterminate
                :size="64"
                :width="6"
              >
              </v-progress-circular>
            </v-layout>
          </v-card>

          <!-- <div style='text-align:center;color:white;' class='mt-2'>
            <div>{{ $appname }}</div>
          </div> -->
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      is_loading: false,
      password: "",
      rules: {
        required: value => !!value || "Required.",
        min: v => v.length >= 6 || "Min 6 characters"
      },
      show1: false,
      username: ""
    };
  },
  computed: {
    server: {
      get() {
        return this.$store.state.server;
      },
      set(val) {
        this.$store.dispatch("setServer", val);
      }
    }
  },
  methods: {
    signIn() {
      const _this = this;
      if (_this.username != "" && _this.password != "") {
        _this.is_loading = true;

        const router = _this.$router;
        const username = _this.username;
        const password = _this.password;

        _this.$http
          .post("/login", {
            username,
            password
          })
          .then(response => {
            response = response.data;

            try {
              if (response.result) {
                _this.$store.dispatch("user/SET", {
                  id: response.user.id,
                  name: response.user.name,
                  username: response.user.username,
                  logged_in: true,
                  token: response.token
                });

                _this.$http.defaults.headers.Authorization = `Bearer ${response.token}`;

                router.push("/");
              }
            } catch (error) {
              this.$log.err(error);
            }
          })
          .catch((/* err */) => {
            _this.alert("Login gagal");
          })
          .finally(() => {
            _this.is_loading = false;
          });
      }
    }
  },
  metaInfo: {
    title: "Masuk"
  }
};
</script>

