export default {
  methods: {
    capitalize(value, transformValue = true) {
      if (!value) return '';
      if (!transformValue) return value;
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
    uppercase(value, transformValue = true) {
      if (!value) return '';
      if (!transformValue) return value;
      value = value.toString();
      return value.toUpperCase();
    },
    lowercase(value, transformValue = true) {
      if (!value) return '';
      if (!transformValue) return value;
      value = value.toString();
      return value.toLowerCase();
    },
    titlecase(value, transformValue = true) {
      if (!value) return '';
      if (!transformValue) return value;

      value = value.toLowerCase().replace(/\b[a-z]/g, letter => letter.toUpperCase());

      return value;
    },
    nl2br(value, is_xhtml) {
      // http://kevin.vanzonneveld.net
      // +   original by: Kevin van Zonneveld (http://kevin.vanzonneveld.net)
      // +   improved by: Philip Peterson
      // +   improved by: Onno Marsman
      // +   improved by: Atli Þór
      // +   bugfixed by: Onno Marsman
      // +      input by: Brett Zamir (http://brett-zamir.me)
      // +   bugfixed by: Kevin van Zonneveld (http://kevin.vanzonneveld.net)
      // +   improved by: Brett Zamir (http://brett-zamir.me)
      // +   improved by: Maximusya
      // *     example 1: nl2br('Kevin\nvan\nZonneveld');
      // *     returns 1: 'Kevin<br />\nvan<br />\nZonneveld'
      // *     example 2: nl2br("\nOne\nTwo\n\nThree\n", false);
      // *     returns 2: '<br>\nOne<br>\nTwo<br>\n<br>\nThree<br>\n'
      // *     example 3: nl2br("\nOne\nTwo\n\nThree\n", true);
      // *     returns 3: '<br />\nOne<br />\nTwo<br />\n<br />\nThree<br />\n'
      var breakTag = (is_xhtml || typeof is_xhtml === 'undefined') ? '<br ' + '/>' : '<br>'; // Adjust comment to avoid issue on phpjs.org display

      return (value + '').replace(/([^>\r\n]?)(\r\n|\n\r|\r|\n)/g, '$1' + breakTag + '$2');
    }
  }
};