import Vue from 'vue';
import stringMix from '@/helper/string';

Vue.filter('capitalize', function (value, transformValue = true) {
  return stringMix.methods.capitalize(value, transformValue);
});

Vue.filter('uppercase', function (value, transformValue = true) {
  return stringMix.methods.uppercase(value, transformValue);
});

Vue.filter('lowercase', function (value, transformValue = true) {
  return stringMix.methods.lowercase(value, transformValue);
});

Vue.filter('titlecase', function (value, transformValue = true) {
  return stringMix.methods.titlecase(value, transformValue);
});

Vue.filter('nl2br', function (value) {
  return stringMix.methods.nl2br(value);
});