import Vue from 'vue';
import Router from 'vue-router'
import PT from '@/views/PT'
import CV from '@/views/CV'
import Koperasi from '@/views/Koperasi'
import Firma from '@/views/Firma'
import Perkumpulan from '@/views/Perkumpulan'
import Yayasan from '@/views/Yayasan'

import Home from '@/views/Home'
import BidangUsaha from '@/views/admin/BidangUsaha'
import KategoriBidang from '@/views/admin/KategoriBidang'
import Harga from '@/views/admin/Harga'
import AdminPt from '@/views/admin/PT'
import AdminCv from '@/views/admin/CV'
import AdminKoperasi from '@/views/admin/Koperasi'
import AdminFirma from '@/views/admin/Firma'
import AdminPerkumpulan from '@/views/admin/Perkumpulan'
import AdminYayasan from '@/views/admin/Yayasan'
import AdminSetting from '@/views/admin/Setting'

import Login from '@/views/Login'
import NotFound from '@/views/NotFound'
import { ifNotAuthenticated, ifAuthenticated } from './guard';

Vue.prototype.$history = window.history;
Vue.use(Router);

const routes = [
  { path: '/', component: Home },
  { path: '/pt', component: PT, name: 'pt_index' },
  { path: '/cv', component: CV, name: 'cv_index' },
  { path: '/koperasi', component: Koperasi, name: 'koperasi_index' },
  { path: '/firma', component: Firma, name: 'firma_index' },
  { path: '/perkumpulan', component: Perkumpulan, name: 'perkumpulan_index' },
  { path: '/yayasan', component: Yayasan, name: 'yayasan_index' },
  { path: '/harga', component: Harga, props: {readOnly: true}, name: 'harga_index' },

  { path: '/pt/:pt_id(\\d+)', component: PT, ifAuthenticated, name: 'admin_pt_view' },
  { path: '/cv/:cv_id(\\d+)', component: CV, ifAuthenticated, name: 'admin_cv_view' },
  { path: '/koperasi/:koperasi_id(\\d+)', component: Koperasi, ifAuthenticated, name: 'admin_koperasi_view' },
  { path: '/firma/:firma_id(\\d+)', component: Firma, ifAuthenticated, name: 'admin_firma_view' },
  { path: '/perkumpulan/:perkumpulan_id(\\d+)', component: Perkumpulan, ifAuthenticated, name: 'admin_perkumpulan_view' },
  { path: '/yayasan/:yayasan_id(\\d+)', component: Yayasan, ifAuthenticated, name: 'admin_yayasan_view' },

  { path: '/admin/kategori-bidang', component: KategoriBidang, ifAuthenticated, name: 'admin_kategori_bidang' },
  { path: '/admin/bidang-usaha', component: BidangUsaha, ifAuthenticated, name: 'admin_bidang_usaha' },
  { path: '/admin/harga', component: Harga, ifAuthenticated, name: 'admin_harga' },

  { path: '/login', component: Login, ifNotAuthenticated },
  { path: '/admin/pt', component: AdminPt, ifAuthenticated, name: 'admin_pt_list' },
  { path: '/admin/cv', component: AdminCv, ifAuthenticated, name: 'admin_cv_list' },
  { path: '/admin/koperasi', component: AdminKoperasi, ifAuthenticated, name: 'admin_koperasi_list' },
  { path: '/admin/firma', component: AdminFirma, ifAuthenticated, name: 'admin_firma_list' },
  { path: '/admin/perkumpulan', component: AdminPerkumpulan, ifAuthenticated, name: 'admin_perkumpulan_list' },
  { path: '/admin/yayasan', component: AdminYayasan, ifAuthenticated, name: 'admin_yayasan_list' },
  { path: '/admin/setting', component: AdminSetting, ifAuthenticated, name: 'admin_setting' },

  { path: '*', component: NotFound }
]

const router = new Router({
  // mode: 'history',
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return to.hash;
    } else if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
});

export default router;
