<template>
  <v-text-field
    :value="value"
    :rules="rules"
    :maxlength="maxLength"
    :label="label"
    ref="text_field"
    prefix="Rp. "
    @keydown="numberOnly"
    @input="removeLeadingZero($event)"
  />
</template>

<script>
import numeral from '@/plugins/numeral'

export default {
  props: {
    label: {
      type: String,
      default: ''
    },
    value: [String, Number],
    maxLength: {
      type: Number,
      default: 14
    },
    rules: {
      type: Array,
      default: () => ([])
    },
  },
  data: () => ({
    
  }),
  created() {

  },
  methods: {
    removeLeadingZero (event) {
      let newValue = event
      if(newValue) {
        newValue = newValue.replace(/^0+(?=\d)/,'')
        newValue = numeral(newValue).format()
      } else if (newValue == '') {
        newValue = '0'
      }
      this.$emit('input', newValue)
    },
    validate () {
      this.$refs.text_field.validate()
    }
  }
}
</script>