import store from '@/store'; // your vuex store

export const ifNotAuthenticated = (to, from, next) => {
  if (!store.state.user.logged_in) {
    next();
    return;
  }
  next('/');
};

export const ifAuthenticated = (to, from, next) => {
  if (store.state.user.logged_in) {
    next();
    return;
  }
  next('/login');
};
