import numeral from '@/plugins/numeral'

export default {
  methods: {
    numberOnly(evt, exception) {
      const systemKey = ['Backspace', 'Delete', 'Escape', 'Tab'];
      const movementKey = ['ArrowLeft', 'ArrowDown', 'ArrowRight', 'ArrowUp', 'Home', 'End'];
      const commandKey = ['a', 'c', 'v', 'x'];

      if (evt.key) {
        if (exception && exception.length) {
          if (exception.indexOf(evt.key) !== -1) {
            return true;
          }
        }
        // Allow: backspace, delete, tab, escape
        if (systemKey.indexOf(evt.key) !== -1 ||
          // Allow: Ctrl+A,Ctrl+C,Ctrl+V, Command+A
          (evt.ctrlKey === true && commandKey.indexOf(evt.key) !== -1) ||
          // Allow: home, end, left, right, down, up
          (movementKey.indexOf(evt.key) !== -1)) {
          // let it happen, don't do anything
          return true;
        }
        // Ensure that it is a number and stop the keydown
        if (parseInt(evt.key) >= 0 && parseInt(evt.key) <= 9) {
          return true;
        }/*  else if (['Control', 'Alt', 'Shift', 'Capslock', 'Tab', 'Shift', 'Enter', 'Backspace', 'Delete', 'ArrowLeft', 'ArrowDown', 'ArrowRight', 'ArrowUp'].indexOf(evt.key) !== -1) {
          return true
        } */

        evt.preventDefault();
      } else {
        var charCode = (evt.which) ? evt.which : evt.keyCode;
        if (charCode > 31 && (charCode < 48 || charCode > 57)) {
          evt.preventDefault();
        }
        return true;
      }
    },

    pastedNumber(event, exception) {
      let paste = (event.clipboardData || window.clipboardData).getData('text');

      paste = paste.split('').filter((val) => {
        if (exception && exception.length) {
          if (exception.indexOf(val) !== -1) {
            return true;
          }
        }
        if (parseInt(val) >= 0 && parseInt(val) <= 9) {
          return true;
        }

        return false;
      }).join('');

      const inputelement = event.target;
      var startPos = inputelement.selectionStart;
      var endPos = inputelement.selectionEnd;
      // const selectedText = inputelement.value.substring(startPos, endPos)
      event.preventDefault();

      inputelement.value = inputelement.value.substring(0, startPos) +
        paste +
        inputelement.value.substring(endPos, inputelement.value.length);

      event.preventDefault();
      return inputelement.value;
    },

    numeralVal(val) {
      return numeral(val).value()
    }
    /* removeLeadingZero(value) {
      if (value) {
        value = value.replace(/^0+(?=\d)/, '')
      }
      return value
    } */
  }
};
