<template lang="html">
  <v-snackbar
    v-model="show"
    :timeout="timeout"
    :top="position=='top'"
    :color="color"
  >
    <v-icon
      color="pink"
      class="mr-3"
    >
      fa-bell
    </v-icon>

    <div style="color:black;">{{ text }}</div>

    <v-btn
      class="pa-0"
      dark
      text
      small
      @click="show = false"
    >
      <v-icon color="pink" style='font-size: 20px;'>fa-times</v-icon>
    </v-btn>
  </v-snackbar>
</template>

<script>

export default {
  data() {
    return {
      show: false,
      timeout: 5000,
      color: 'white',
      position: 'top',
      type: 1,
      type_array: {
        1: 'success', 2: 'info', 3: 'warning', 4: 'error',
      },
      icon_array: {
        1: 'check-circle', 2: 'info-circle', 3: 'exclamation-circle', 4: 'exclamation-circle',
      },
    };
  },
  computed: {
    text() {
      const _alert = this.$store.getters['snackbar/GET_ALERT'];
      return _alert.text;
    },
  },
  watch: {
    show(newVal) {
      if (!newVal) {
        this.$store.dispatch('snackbar/RESET_ALERT');
      }
    },
    text(newVal){
      const _alert = this.$store.getters['snackbar/GET_ALERT'];
      if (_alert.priority == 3) {
        this.timeout = 0;
        // this.color = 'error';
      } else if (_alert.priority == 2) {
        this.timeout = 6000;
        // this.color = null;
      } else {
        this.timeout = 3000;
        // this.color = null;//black
      }
      if (newVal) {
        this.show = true;
      }
    }
  },
};
</script>