import { mapActions } from 'vuex';
const FileTypeBrowser = require('file-type/browser');

export default {
  data() {
    return {
      tagsToReplace : {
        '&': '&amp;',
        '<': '&lt;',
        '>': '&gt;',
      }
    };
  },
  methods: {
    ...mapActions('snackbar', { alert: 'SET_ALERT' }),
    /* showDialog(component_name, payload) {
      let _component = null;
      const _this = this;
      
      try {
        if (payload) {
          _this.dialog_picker.payload = payload;
        }
        _component = component_name;

        _this.dialog_picker.show = true;
      } catch (err) {
        _component = null;
        _this.$log.error(err);
        _this.dialog_picker.show = false;
      }
      _this.dialog_picker.component = _component;
    }, */
    async blobToObject(data) {
      const theBlob = new Blob([data])

      const fileType = await FileTypeBrowser.fromBlob(theBlob)
      const url = window.URL.createObjectURL(theBlob);

      return { url, fileType}
    },

    downloadAttachment(option) {
      const _this = this

      _this.$http
        .get('/' + option.badan + '/' + option.id + (option.type ? '/' + option.type : '') + '/download', {
          cache: true,
          headers: {
            'Accept': '*/*'
          },
          responseType: 'blob',
          params: { path: option.path },
        })
        .then(async (res) => {
          const theBlob = await _this.blobToObject(res.data)
          // const theBlob = new Blob([res.data])

          const fileType = theBlob.fileType
          // const url = window.URL.createObjectURL(theBlob);
          const link = document.createElement('a');
          link.href = theBlob.url;
          link.setAttribute('download', option.badan+'_'+option.nama_badan + '_' + option.nama + '_' + option.kedudukan + '_' + option.type + '.' + fileType.ext);
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        });
    },
    escapeHTML(str) {
      return str.replace(/[&<>]/g, tag => this.tagsToReplace[tag] || tag)
    },
    genHighlight(text) {
      return `<span class="v-list-item__mask">${this.escapeHTML(text)}</span>`
    },
    genFilteredText(text, searchInput) {
      text = text || ''

      if (!searchInput) return this.escapeHTML(text)

      const { start, middle, end, found } = this.getMaskedCharacters(text, searchInput)
      
      if (found) {
        return `${this.escapeHTML(start)}${this.genHighlight(middle)}${this.escapeHTML(end)}`
      } else {
        return `${this.escapeHTML(start)}${this.escapeHTML(middle)}${this.escapeHTML(end)}`
      }
    },
    getMaskedCharacters(text, searchInput)/* : {
      start: string
      middle: string
      end: string
    }  */{
      searchInput = (searchInput || '').toString().toLocaleLowerCase()
      const index = text.toLocaleLowerCase().indexOf(searchInput)

      if (index < 0) return { start: '', middle: text, end: '', found: false }

      const start = text.slice(0, index)
      const middle = text.slice(index, index + searchInput.length)
      const end = text.slice(index + searchInput.length)
      return { start, middle, end, found: true }
    }
  }
};