<template>
  <v-container >
    <v-row>
      <v-col
        cols="12"
        xs="12"
      >
        <v-data-table
          :headers="table.headers"
          :items="table.items"
          :items-per-page="5"
          :footer-props="{
            'items-per-page-options': [20,50,100,200,-1]
          }"
          class="elevation-1"
          :loading="table.loading"
          :search="table.search"
          loading-text="Memuat... Harap Tunggu"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title>Kategori Bidang Usaha</v-toolbar-title>
              
              <v-divider
                class="mx-4"
                inset
                vertical
              />

              <v-btn 
                color="info"
                fab
                small
                depressed
                @click="loadData(false)"
              >
                <v-icon
                  :class="table.loading ? 'fa-spin' : ''"
                  small
                >fa-redo fa-sm</v-icon>
              </v-btn>
              <v-spacer></v-spacer>

              <v-text-field
                v-model="table.search"
                append-icon="mdi-magnify"
                label="Search"
                color="info"
                single-line
                hide-details
                class="mr-4"
                clearable
              />
                
              <v-btn
                color="info"
                dark
                fab
                small
                depressed
                @click="showForm"
              >
                <v-icon
                  small
                >fa-plus fa-sm</v-icon>
              </v-btn>
              
              <v-dialog v-model="dialog" max-width="700px">
                <v-card v-show="form.loading" >
                  <v-card-text>
                    Harap Tunggu
                    <v-progress-linear
                      indeterminate
                      color="primary"
                      class="mb-0"
                    />
                  </v-card-text>
                </v-card>

                <v-card v-show="!form.loading">
                  <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" xs="12" sm="4">
                          <v-text-field v-model="form.input.kode" ref="form_kode" label="Kode"/>
                        </v-col>
                        <v-col cols="12" xs="12" sm="8">
                          <v-text-field v-model="form.input.nama" label="Nama"/>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="grey" text @click="close">Batal</v-btn>
                    <v-btn color="blue darken-1" text @click="save">Simpan</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>

          <template v-slot:item.action="{ item }">
            <v-icon
              small
              class="mr-2"
              @click="editItem(item)"
            >
              fa-pencil-alt
            </v-icon>
            <v-icon
              small
              @click="deleteItem(item)"
            >
              fa-trash
            </v-icon>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        form: {
          default: {
            id: '',
            kode: '',
            nama: ''
          },
          input: {
            id: '',
            kode: '',
            nama: ''
          },
          loading: false,
          valid: false
        },
        table: {
          headers: [
            {
              text: 'Kode',
              align: 'left',
              sortable: false,
              value: 'kode',
            },
            { text: 'Nama', value: 'nama', sortable: false },
            { text: '', value: 'action', sortable: false },
          ],
          index: -1,
          items: [],
          loading: true,
          search: ''
        }
      }
    },

    computed: {
      formTitle () {
        return this.table.index === -1 ? 'Kategori Baru' : 'Ubah Kategori'
      },
    },

    created () {
      this.loadData()
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    methods: {
      close () {
        this.dialog = false
        setTimeout(() => {
          this.form.input = Object.assign({}, this.form.default)
          this.table.index = -1
        }, 300)
      },

      delete (item, index) {
        const _this = this
        
        _this.table.loading = true

        _this.$http.delete('/kategori_bidang/'+item.id)
          .then((/* response */) => {
            _this.alert('Berhasil menghapus kategori');
            _this.table.items.splice(index, 1)
            _this.close()
          })
          .finally(() => {
            _this.table.loading = false
          });
      },

      deleteItem (item) {
        const index = this.table.items.indexOf(item)
        confirm('Anda yakin akan menghapus kategori ini?') && this.delete(item, index)
      },

      editItem (item) {
        this.table.index = this.table.items.indexOf(item)
        this.form.input = Object.assign({}, item)
        this.showForm()
      },

      httpGet(url, data, cache) {
        const _this = this;
        const options = { params: data, cache: true };
        if (cache === false || this.$route.query.new) {
          options.forceUpdate = true;
        }

        return _this.$http
          .get(url, options)
          .then((response) => {
            return response.data;
          });
      },

      loadData (cache) {
        const _this = this;
        
        _this.table.loading = true
        _this.httpGet('/kategori_bidang', {}, cache)
          .then((response) => {
            _this.table.items = response.data
          })
          .finally(() => {
            _this.table.loading = false
          })
      },

      save () {
        const _this = this
        
        _this.form.loading = true

        if (this.table.index > -1) {
          _this.$http.patch('/kategori_bidang/'+_this.form.input.id, _this.form.input)
            .then((response) => {
              _this.alert('Berhasil mengubah kategori');
              Object.assign(_this.table.items[_this.table.index], response.data.data)
              _this.close()
            })
            .finally(() => {
              _this.form.loading = false
            });
        } else {
          _this.$http.post('/kategori_bidang', _this.form.input)
            .then((response) => {
              _this.alert('Berhasil menambah kategori');
              _this.table.items.push(response.data.data)
              _this.close()
            })
            .finally(() => {
              _this.form.loading = false
            });
        }
      },

      showForm () {
        const _this = this
        _this.dialog=true

        _this.$nextTick(function () {
          _this.$refs.form_kode.focus();
        });
      }
    },

    metaInfo: {
      title: 'Daftar Kategori Bidang Usaha',
    }
  }
</script>