import Vue from 'vue';
import axios from 'axios';
import store from '@/store';
import router from '@/router';
import {
  cacheAdapterEnhancer,
  throttleAdapterEnhancer,
} from 'axios-extensions';
// var LRUCache = require("lru-cache");

const axios_instance = axios.create({
  baseURL: process.env.VUE_APP_API_HOST + ':' + process.env.VUE_APP_API_PORT + process.env.VUE_APP_API_BASE,
  // timeout: 10000,
  headers: {
    Accept: 'application/json',
    Authorization: `Bearer ${store.state.user.token}`,
  },
  adapter: throttleAdapterEnhancer(
    cacheAdapterEnhancer(axios.defaults.adapter /*, {
      defaultCache: new LRUCache(axiosMaxAgeCache)
    } */)
  ),
});

axios_instance.interceptors.request.use(function (config) {
  return config;
}, function (error) {
  window.log.error('request error');
  window.log.error(error);
  return Promise.reject(error);
});

axios_instance.interceptors.response.use((response) => {
  const the_response = JSON.parse(JSON.stringify(response));
  window.log.debug('['+the_response.config.method.toUpperCase()+']' + the_response.config.url);
  window.log.debug('Response: ');
  window.log.debug(the_response);
  return response;
}, function (error) {
  store.commit(
    'snackbar/SET_ALERT',
    'Oops, Something went wrong'
  );

  if (error.response) {
    // If reponse is unauthorized (401) then redirect user to login page
    if (error.response.status == 401) {
      store.dispatch('user/CLEAR');
      router.push('/login');
      store.commit(
        'snackbar/SET_ALERT',
        'Oops, Silahkan login'
      );
    } else if (error.response.status == 403) {
      store.commit(
        'snackbar/SET_ALERT',
        'Sorry, but you are not authorized'
      );
    } else if (error.response.status == 404) {
      if (error.response.data.message) {
        store.commit(
          'snackbar/SET_ALERT',
          error.response.data.message
        );
      } else {
        store.commit(
          'snackbar/SET_ALERT',
          'Sorry, Resource is not found'
        );
      }
    } else if (error.response.data.errors) {
      const errors = error.response.data.errors;
      store.commit(
        'snackbar/SET_ALERT',
        errors[Object.keys(errors)[0]][0]
      );
    } else if (error.response.data.message) {
      store.commit(
        'snackbar/SET_ALERT',
        error.response.data.message
      );
    } else {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      window.log.error(error.response.data);
      window.log.error(error.response.status);
      window.log.error(error.response.headers);
      store.commit(
        'snackbar/SET_ALERT',
        'Oops, something went wrong'
      );
    }

    return Promise.reject(error.response);
  } else if (error.request) {
    window.log.error('The request was made but no response was received');
    // The request was made but no response was received
    // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
    // http.ClientRequest in node.js
    window.log.error(error.config);
    window.log.error(error.request);
    return Promise.reject(error.request);
  } else {
    // Something happened in setting up the request that triggered an Error
    window.log.error(error.config);
    window.log.error(error.message);
    return Promise.reject(error.message);
  }

});

Vue.prototype.$http = axios_instance;

export { axios_instance };


