<template>
  <v-container >
    <v-row>
      <v-col
        cols="12"
        xs="12"
      >
        <v-data-table
          :headers="table.headers"
          :items="table.items"
          :items-per-page="5"
          :footer-props="{
            'items-per-page-options': [20,50,100,200,-1]
          }"
          class="elevation-1"
          :loading="table.loading"
          :search="table.search"
          loading-text="Memuat... Harap Tunggu"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title>Daftar Perkumpulan</v-toolbar-title>
              
              <v-divider
                class="mx-4"
                inset
                vertical
              />

              <v-btn 
                color="info"
                fab
                small
                depressed
                @click="loadData(false)"
              >
                <v-icon
                  :class="table.loading ? 'fa-spin' : ''"
                  small
                >fa-redo fa-sm</v-icon>
              </v-btn>
              
              <v-spacer></v-spacer>
              
              <v-text-field
                v-model="table.search"
                append-icon="mdi-magnify"
                label="Search"
                color="info"
                single-line
                hide-details
                clearable
              />
            </v-toolbar>
          </template>

          <template v-slot:item="{item}">
            <tr>
              <td>{{item.nama_perkumpulan}}</td>
              <td>{{item.nama_provinsi}}</td>
              <td>{{item.nama_kotkab}}</td>
              <td>{{item.modal_dasar | numberFormat}}</td>
              <td>
                <div>{{item.pemegang_utama}}</div>
              </td>
              <td>{{item.nomor_handphone}}</td>
              
              <td>
                <v-icon
                  x-small
                  class="mr-2"
                  @click="$router.push({
                            name: 'admin_perkumpulan_view',
                            params: {
                              perkumpulan_id: item.id
                            }
                          })"
                >
                  fa-pencil-alt
                </v-icon>
                <v-icon
                  x-small
                  @click="!loadingDelete && deleteItem(item)"
                >
                  fa-trash
                </v-icon>
              </td>
            </tr>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        table: {
          headers: [
            {
              text: 'Nama',
              align: 'left',
              sortable: false,
              value: 'nama_perkumpulan',
            },
            { text: 'Provinsi', value: 'nama_provinsi', sortable: false },
            { text: 'Kota/Kabupaten', value: 'nama_kotkab', sortable: false },
            { text: 'Modal Dasar', value: 'modal_dasar', sortable: false },
            { text: 'Ketua', value: 'pemegang_utama', sortable: false },
            { text: 'Handphone', value: 'nomor_handphone', sortable: false },
            { text: '', value: 'action', sortable: false },
          ],
          index: -1,
          items: [],
          loading: true,
          search: ''
        },
        loadingDelete: false,
      }
    },

    created () {
      this.loadData()
    },

    methods: {
      delete (item, index) {
        const _this = this
        
        _this.table.loading = _this.loadingDelete = true

        _this.$http.delete('/perkumpulan/'+item.id)
          .then((/* response */) => {
            _this.alert('Berhasil menghapus Perkumpulan');
            _this.table.items.splice(index, 1)
            _this.close()
          })
          .finally(() => {
            _this.table.loading = _this.loadingDelete = false
          });
      },

      deleteItem (item) {
        const index = this.table.items.indexOf(item)
        confirm('Anda yakin akan menghapus Perkumpulan ini?') && this.delete(item, index)
      },

      httpGet(url, data, cache) {
        const _this = this;
        const options = { params: data, cache: true };
        if (cache === false || this.$route.query.new) {
          options.forceUpdate = true;
        }

        return _this.$http
          .get(url, options)
          .then((response) => {
            return response.data;
          })
      },

      loadData (cache) {
        const _this = this;
        
        _this.table.loading = true
        _this.httpGet('/perkumpulan', {}, cache)
          .then((response) => {
            _this.table.items = response.data
          })
          .finally(() => {
            _this.table.loading = false
          })
      },

    },

    metaInfo: {
      title: 'Daftar Perkumpulan',
    }
  }
</script>