<template>
  <v-text-field
    :value="value"
    :label="label"
    :rules="[rules.cellphonePrefix, rules.required, rules.min(10), rules.max(12)]"
    maxlength="12"
    counter
    @keydown="numberOnly"
    @input="$emit('input', $event)"
  />
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      default: 'No. Handphone'
    },
    value: [String, Number]
  },
  data: () => ({
    rules: {
      cellphonePrefix: v => RegExp("^(08)\\d+$").test(v) || "Awali dengan '08'",
      max(maxNum) {
        return v => (v || '').length <= maxNum || 'Maksimal '+maxNum+' karakter'
      },
      min(minNum) {
        return v => (v || '').length >= minNum || 'Minimum '+minNum+' karakter'
      },
      required: v => !!v || 'Harus diisi',
    }
  }),
  created() {

  },
  methods: {
    validate () {
      this.$refs.text_field.validate()
    }
  }
}
</script>