                      <v-file-input
                        v-model="form4.input.npwp[3]"
                        :label="'NPWP '+form3.input.pemegang_utama[3].kedudukan+' ('+form3.input.pemegang_utama[3].nama+')'"
                        hint="File gambar atau pdf"
                        persistent-hint
                        show-size
                        accept="image/*,application/pdf" 
                      />
                      <attachment-thumbnail
                        :file="form4.npwp_file[3]"
                        :ext="form4.npwp_ext[3]"
                        :option="{
                            badan: 'perkumpulan',
                            id: id,
                            nama_badan: form1.input.nama_perkumpulan,
                            type: 'npwp',
                            path: form4.npwp_path[3],
                            nama: form3.input.pemegang_utama[3].nama,
                            kedudukan: form3.input.pemegang_utama[3].kedudukan
                          }"
                      />
                    </v-col>
                  </v-row>
                  
                  <!-- TAMBAHAN -->
                  <v-row
                    v-for="(item, index) in form3.input.pemegang_tambahan"
                    v-bind:key="'tambahan-'+index"
                  >
                    <v-col
                      cols="12"
                      sm="6"
                      xs="12"
                    >
                      <v-file-input
                        v-model="form4.input.ktp[index+4]"
                        :rules="[rules.required]"
                        :label="'KTP '+item.kedudukan+' ('+item.nama+')'"
                        hint="File gambar atau pdf"
                        persistent-hint
                        show-size
                        accept="image/*,application/pdf" 
                        class="required"
                      />
                      <attachment-thumbnail
                        :file="form4.ktp_file[index+4]"
                        :ext="form4.ktp_ext[index+4]"
                        :option="{
                            badan: 'perkumpulan',
                            id: id,
                            nama_badan: form1.input.nama_perkumpulan,
                            type: 'ktp',
                            path: form4.ktp_path[index+4],
                            nama: item.nama,
                            kedudukan: item.kedudukan
                          }"
                      />
                    </v-col>

                    <v-col
                      cols="12"
                      sm="6"
                      xs="12"
                    >
                      <v-file-input
                        v-model="form4.input.npwp[index+4]"
                        :label="'NPWP '+item.kedudukan+' ('+item.nama+')'"
                        hint="File gambar atau pdf"
                        persistent-hint
                        show-size
                        accept="image/*,application/pdf" 
                      />
                      <attachment-thumbnail
                        :file="form4.npwp_file[index+4]"
                        :ext="form4.npwp_ext[index+4]"
                        :option="{
                            badan: 'perkumpulan',
                            id: id,
                            nama_badan: form1.input.nama_perkumpulan,
                            type: 'npwp',
                            path: form4.npwp_path[index+4],
                            nama: item.nama,
                            kedudukan: item.kedudukan
                          }"
                      />
                    </v-col>
                  </v-row>
                  
                  <v-row style="margin-top:200px;">
                    <v-col
                      cols="12"
                    >
                      <v-checkbox
                        v-model="form4.setuju"
                        :rules="[v => !!v || 'Anda harus menyatakan kebenaran data']"
                        label="Dengan ini Saya menyatakan semua data yang Saya masukan adalah benar"
                      />
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>

              <v-btn
                color="primary"
                :loading="loading_submit"
                @click="submit"
              >
                Kirim
              </v-btn>

              <v-btn
                text
                @click="form_step = 3"
              >Kembali</v-btn>
            </v-stepper-content>
          </v-stepper-items>
          <v-alert
            text
            color="info"
            class="ma-0"
          >
            <span style="color:red;">*</span> Wajib diisi
          </v-alert>
              
        </v-stepper>
      </v-col>
    </v-row>
    <submitted-dialog
      v-model="submitted_dialog"
      :data="formatted_data"
    />
  </v-container>
</template>

<script>
  import InputMoney from '@/components/InputMoney';
  import InputCellphone from '@/components/InputCellphone';
  import SubmittedDialog from '@/components/SubmittedDialog';
  import AttachmentThumbnail from '@/components/AttachmentThumbnail';
  import numeral from '@/plugins/numeral'

  export default {
    components: { InputMoney, InputCellphone, SubmittedDialog, AttachmentThumbnail },
    data: () => ({
      biaya_data: {
        "1": {
          normal: 0,
          khusus: 0,
        },
        "2": {
          normal: 0,
          khusus: 0,
        },
        "3": {
          normal: 0,
          khusus: 0,
        },
      },
      form_step: 1,
      form1: {
        input: {
          kategori_modal: "1",
          modal_dasar: 0,
          nama_perkumpulan: '',
          nama_alternatif: [''],
        },
        kategori_modal: {
          "1": 1000000000,
          "2": 10000000000,
          "3": 10000000001,
        },
        wilayah_modal: {
          "1": 50000000,
          "2": 150000000,
          "3": 500000000,
        },
        valid: false
      },
      form2: {
        input: {
          alamat: "",
          provinsi: '',
          kotkab: '',
          kecamatan: '',
          kegiatan: '',
          kelurahan: '',
          rt: '',
          rw: '',
          kodepos: '',
          kode_telpon: '',
          nomor_telpon: '',
          nomor_handphone: '',
          email: '',
        },
        loading_regencies: false,
        loading_provinces: true,
        loading_districts: false,
        loading_urbans: false,
        regencies: [],
        provinces: [],
        districts: [],
        urbans: [],
        valid: false
      },
      form3: {
        input: {
          pemegang_utama: [
            {
              nama: '',
              kedudukan: 'pengawas',
              hp: '',
              ktp: null,
              npwp: null
            },
            {
              nama: '',
              kedudukan: 'ketua',
              hp: '',
              ktp: null,
              npwp: null
            },
            {
              nama: '',
              kedudukan: 'sekretaris',
              hp: '',
              ktp: null,
              npwp: null
            },
            {
              nama: '',
              kedudukan: 'bendahara',
              hp: '',
              ktp: null,
              npwp: null
            }
          ],
          pemegang_tambahan: [],
        },
        pemegang_tambahan: false,
        table: {
          defaultItem: {
            nama: '',
            kedudukan: '',
            hp: ''
          },
          dialog: false,
          editedItem: {
            nama: '',
            kedudukan: '',
            hp: ''
          },
          headers: [
            {
              text: 'Nama Lengkap',
              align: 'left',
              sortable: false,
              value: 'nama'
            },
            { text: 'Kedudukan', sortable: false, value: 'kedudukan'},
            { text: 'HP', sortable: false, value:'hp'},
            { text: '', sortable: false, value:'action'}
          ],
          index: -1
        },
        valid: false,
      },
      form4: {
        input: {
          ktp: [],
          npwp: [],
        },
        ktp_path: [],
        ktp_ext: [],
        ktp_file: [],
        npwp_path: [],
        npwp_ext: [],
        npwp_file: [],
        setuju: false,
        valid: false,
      },
      form1_default: {
        kategori_modal: "1",
        modal_dasar: 0,
        nama_perkumpulan: '',
        nama_alternatif: [''],
      },
      form2_default: {
        alamat: "",
        provinsi: '',
        kotkab: '',
        kecamatan: '',
        kegiatan: '',
        kelurahan: '',
        rt: '',
        rw: '',
        kodepos: '',
        kode_telpon: '',
        nomor_telpon: '',
        nomor_handphone: '',
        email: '',
      },
      form3_default: {
        pemegang_utama: [
          {
            nama: '',
            kedudukan: 'pengawas',
            hp: '',
            ktp: null,
            npwp: null
          },
          {
            nama: '',
            kedudukan: 'ketua',
            hp: '',
            ktp: null,
            npwp: null
          },
          {
            nama: '',
            kedudukan: 'sekretaris',
            hp: '',
            ktp: null,
            npwp: null
          },
          {
            nama: '',
            kedudukan: 'bendahara',
            hp: '',
            ktp: null,
            npwp: null
          }
        ],
        pemegang_tambahan: [],
      },
      form4_default: {
        ktp: [],
        npwp: [],
      },
      formatted_data: {},
      id: null,
      loading_submit: false,
      rules: {
        minimum2words: v => (!v || (!!v && v.split(' ').length >= 2)) || 'Minimal dua kata',
        email: v => (/.+@.+/.test(v)) || 'Email tidak valid',
        required: v => !!v || 'Harus diisi',
        arrayRequired: v => (v.length > 0) || 'Harus diisi',
        max(maxNum) {
          return v => (v || '').length <= maxNum || 'Maksimal '+maxNum+' karakter'
        },
        min(minNum) {
          return v => (v || '').length >= minNum || 'Minimum '+minNum+' karakter'
        }
      },
      submitted_dialog: false,
      view: ''
    }),

    computed: {
      biaya () {
        return this.biaya_data[this.form1.input.kategori_modal]
      },
      formTitle () {
        return this.form3.table.index === -1 ? 'Tambah' : 'Ubah'
      },
      modal_dasar_val () {
        return this.numeralVal(this.form1.input.modal_dasar)
      },
      rule_modal_dasar () {
        let rules = [this.rules.required]
        if (this.form1.input.kategori_modal == '1') {
          rules.push(() => {
            return (this.modal_dasar_val > 0 && this.modal_dasar_val<=this.form1.kategori_modal[this.form1.input.kategori_modal]) || 's/d 1 Milyar'
          })
        } else if (this.form1.input.kategori_modal == '2') {
          rules.push(() => {
            return (this.modal_dasar_val > this.form1.kategori_modal['1'] && this.modal_dasar_val<=this.form1.kategori_modal[this.form1.input.kategori_modal]) || 's/d 10 Milyar'
          })
        } else if (this.form1.input.kategori_modal == '3'){
          rules.push(() => {
            return (this.modal_dasar_val >= this.form1.kategori_modal["3"]) || 'Lebih dari 10 Milyar'
          })
        } else {
          rules.push(() => true)
        }
        return rules
      }
    },

    async created() {
      this.loadHarga()

      if (this.$route.params.perkumpulan_id) {
        this.id = this.$route.params.perkumpulan_id
        const response = await this.httpGet('/perkumpulan/'+this.id, {}, false)
        this.initForm(response.data)
      }
    },

    watch: {
      form_step (val) {
        if (val == 2) {
          this.loadProvinces()
        } else if (val==4 && this.id ) {
          this.loadAttachments()
        }
      },
      '$route' (/* to, from */) {
        if (this.id){
          this.form_step = 1
          this.resetForm()
        }
      },
      'form1.input.kategori_modal': function() {
        this.$nextTick(()=>{
          this.$refs.ref_modal_dasar.validate()
        })
      }
    },

    methods: {
      addAlternatif (index) {
        if (this.form1.input.nama_alternatif[index] && this.form1.input.nama_alternatif[index].split(' ').length >= 2) {
          this.form1.input.nama_alternatif.push('')
        } else {
          this.alert('Nama alternatif minimal 2 kata')
        }
      },

      close () {
        this.form3.table.dialog = false
        setTimeout(() => {
          this.form3.table.editedItem = Object.assign({}, this.form3.table.defaultItem)
          this.form3.table.index = -1
        }, 300)
      },

      deleteItem (item) {
        let theTable = this.form3.input.pemegang_tambahan
        const index = theTable.indexOf(item)
        theTable.splice(index, 1)
      },

      focus_kodepos () {
        const _this = this
        _this.$nextTick(function () {
          _this.$refs.kodepos.focus();
        });
      },

      httpGet(url, data, cache) {
        const _this = this;
        const options = { params: data, cache: true };
        if (cache === false || this.$route.query.new) {
          options.forceUpdate = true;
        }

        return _this.$http
          .get(url, options)
          .then((response) => {
            return response.data;
          });
      },

      initForm (data) {
        this.view = data.perkumpulan.nama_perkumpulan
        this.form1.input.nama_perkumpulan = data.perkumpulan.nama_perkumpulan
        if (data.perkumpulan.nama_alternatif_1) {
          this.form1.input.nama_alternatif[0] = data.perkumpulan.nama_alternatif_1
        }
        if (data.perkumpulan.nama_alternatif_2) {
          this.form1.input.nama_alternatif[1] = data.perkumpulan.nama_alternatif_2
        }
        if (data.perkumpulan.nama_alternatif_3) {
          this.form1.input.nama_alternatif[2] = data.perkumpulan.nama_alternatif_3
        }
        
        this.form1.input.kategori_modal = data.perkumpulan.kategori_modal
        this.form1.input.modal_dasar = numeral(data.perkumpulan.modal_dasar).format()
        
        //FORM 2
        this.form2.input.alamat = data.perkumpulan.alamat
        this.form2.provinces = [{
          id: data.perkumpulan.provinsi,
          name: data.perkumpulan.nama_provinsi
        }]
        this.form2.input.provinsi = data.perkumpulan.provinsi

        this.form2.regencies = [{
          id: data.perkumpulan.kotkab,
          name: data.perkumpulan.nama_kotkab
        }]
        this.form2.input.kotkab = data.perkumpulan.kotkab

        this.form2.districts = [{
          id: data.perkumpulan.kecamatan,
          name: data.perkumpulan.nama_kecamatan
        }]
        this.form2.input.kecamatan = data.perkumpulan.kecamatan

        this.form2.urbans = [{
          id: data.perkumpulan.kelurahan,
          name: data.perkumpulan.nama_kelurahan
        }]
        this.form2.input.kelurahan = data.perkumpulan.kelurahan

        this.form2.input.rt = ''+data.perkumpulan.rt
        this.form2.input.rw = ''+data.perkumpulan.rw
        this.form2.input.kodepos = data.perkumpulan.kodepos
        this.form2.input.kode_telpon = data.perkumpulan.kode_telpon || ''
        this.form2.input.nomor_telpon = data.perkumpulan.nomor_telpon || ''
        this.form2.input.nomor_handphone = data.perkumpulan.nomor_handphone
        this.form2.input.email = data.perkumpulan.email

        this.form2.input.kegiatan = data.perkumpulan.kegiatan

        //FORM 3 & 4
        this.form4.ktp_path = []
        this.form4.npwp_path = []
        this.form3.input.pemegang_utama = []
        for(let idx in data.perkumpulan_pemegang) {
          if (data.perkumpulan_pemegang[idx].tipe == '1') {
            this.form3.input.pemegang_utama.push(data.perkumpulan_pemegang[idx])
            this.form4.ktp_path.push(data.perkumpulan_pemegang[idx].ktp)
            if (data.perkumpulan_pemegang[idx].npwp){
              this.form4.npwp_path[idx] = (data.perkumpulan_pemegang[idx].npwp)
            }
          }
        }
        this.form3.input.pemegang_tambahan = []
        for(let idx in data.perkumpulan_pemegang) {
          if (data.perkumpulan_pemegang[idx].tipe == '2') {
            this.form3.input.pemegang_tambahan.push(data.perkumpulan_pemegang[idx])
            this.form4.ktp_path.push(data.perkumpulan_pemegang[idx].ktp)
            if (data.perkumpulan_pemegang[idx].npwp){
              this.form4.npwp_path[idx] = (data.perkumpulan_pemegang[idx].npwp)
            }
            this.form3.pemegang_tambahan = true
          }
        }
      },

      async loadAttachments () {
        const _this = this
        _this.form4.ktp_ext = []
        _this.form4.ktp_file = []
        _this.form4.npwp_ext = []
        _this.form4.npwp_file = []

        for(let idx in _this.form4.ktp_path) {
          _this.$http
            .get('/perkumpulan/'+_this.id+'/file', {
              cache: true,
              headers: {
                'Accept': '*/*'
              },
              params: {path: _this.form4.ktp_path[idx] }, 
              responseType: 'blob'
            })
            .then(async (res) => {
              const blobObject = await _this.blobToObject(res.data)
              _this.form4.ktp_ext.push(blobObject.fileType.ext)
              _this.form4.ktp_file.push(blobObject.url)
            })
            .catch((err) => {
              _this.$log.debug(err)
            });
        }

        for(let idx in _this.form4.npwp_path) {
          _this.$http
            .get('/perkumpulan/'+_this.id+'/file', {
              cache: true,
              headers: {
                'Accept': '*/*'
              },
              params: {path: _this.form4.npwp_path[idx] }, 
              responseType: 'blob'
            })
            .then(async (res) => {
              const blobObject = await _this.blobToObject(res.data)
              _this.$set(_this.form4.npwp_ext, idx, blobObject.fileType.ext)
              _this.$set(_this.form4.npwp_file, idx, blobObject.url)
            })
            .catch((err) => {
              _this.$log.debug(err)
            });
        }
      },

      loadHarga () {
        const _this = this

        _this.$http
          .get('/harga/perkumpulan')
          .then((response) => {
            const dataHarga = [...response.data.data]
            _this.biaya_data["1"] = dataHarga[0];
            _this.biaya_data["2"] = dataHarga[1];
            _this.biaya_data["3"] = dataHarga[2];
          });
      },

      loadProvinces () {
        const _this = this;
        
        _this.form2.loading_provinces = true
        _this.httpGet('/provinces')
          .then((response) => {
            _this.form2.provinces = response.data
          })
          .finally(() => {
            _this.form2.loading_provinces = false
          })
      },

      loadRegencies () {
        const _this = this;
        
        _this.form2.loading_regencies = true
        _this.httpGet('/regencies?province_id='+_this.form2.input.provinsi)
          .then((response) => {
            _this.form2.regencies = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kotkab.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_regencies = false
          })
      },

      loadDistricts () {
        const _this = this;
        
        _this.form2.loading_districts = true
        _this.httpGet('/districts?regency_id=' + _this.form2.input.kotkab)
          .then((response) => {
            _this.form2.districts = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kecamatan.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_districts = false
          })
      },

      loadUrbans () {
        const _this = this;
        
        _this.form2.loading_urbans = true
        _this.httpGet('/urbans?district_id=' + _this.form2.input.kecamatan)
          .then((response) => {
            _this.form2.urbans = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kelurahan.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_urbans = false
          })
      },

      nextStep(theFormRef) {
        if (theFormRef.validate()) this.form_step++
      },

      removeAlternatif () {
        this.form1.input.nama_alternatif.pop()
      },

      resetForm () {
        const _this = this

        _this.id = null
        _this.view = null
        _this.$refs.form1.resetValidation()
        _this.$refs.form2.resetValidation()
        _this.$refs.form3.resetValidation()
        _this.$refs.form4.resetValidation()

        _this.form1.input = {... _this.form1_default}
        _this.form2.input = {... _this.form2_default}
        _this.form3.input = {... _this.form3_default}
        _this.form4.input = {... _this.form4_default}
        _this.form3.pemegang_tambahan = false

        _this.form4.ktp_path = []
        _this.form4.ktp_ext = []
        _this.form4.ktp_file = []
        _this.form4.npwp_path = []
        _this.form4.npwp_ext = []
        _this.form4.npwp_file = []

        _this.form4.setuju = false

      },

      save () {
        let theTable = this.form3.input.pemegang_tambahan
        const theForm = this.form3.table.editedItem

        if(!theForm.nama || !theForm.kedudukan || !theForm.hp) {
          this.alert("Mohon isi semua kolom")
          return false
        }

        if (this.form3.table.index > -1) {
          Object.assign(theTable[this.form3.table.index], theForm)
        } else {
          theTable.push(theForm)
        }
        this.close()
      },

      showFormDialog(item)   {
        if (item) {
          this.form3.table.index = this.form3.input.pemegang_tambahan.indexOf(item)
          this.form3.table.editedItem = Object.assign({}, item)
        }
        this.form3.table.dialog = true
      },
      
      submit () {
        const _this = this

        if (_this.$refs.form4.validate()) {
          const postData = {..._this.form1.input, ..._this.form2.input, ..._this.form3.input, ..._this.form4.input}

          const formData = new FormData();
          formData.append('biaya', _this.biaya.khusus)
          
          for ( var key in postData ) {
            formData.append(key, postData[key]);
          }

          formData.delete('nama_alternatif')
          for(let idx in postData.nama_alternatif) {
            formData.append('nama_alternatif[]', postData.nama_alternatif[idx])
          }

          formData.delete('modal_dasar')
          formData.set("modal_dasar", this.modal_dasar_val)

          formData.delete('ktp')
          for(let idx in postData.ktp) {
            formData.append('ktp['+idx+']', postData.ktp[idx])
          }

          formData.delete('npwp')
          for(let idx in postData.npwp) {
            formData.append('npwp['+idx+']', postData.npwp[idx])
          }
          
          formData.delete('pemegang_utama')
          formData.set("pemegang_utama", JSON.stringify(postData.pemegang_utama))
          formData.delete('pemegang_tambahan')
          formData.set("pemegang_tambahan", JSON.stringify(postData.pemegang_tambahan))
        
          _this.loading_submit = true
          _this.submitted_dialog = true

          if (_this.id) {
            _this.$http
              .post('/perkumpulan/'+_this.id, formData, {
                headers: {
                  'Content-Type': 'multipart/form-data'
                }
              })
              .then(() => {
                _this.alert('Sukses, perubahan data Perkumpulan berhasil')
                
                _this.$router.push({name:'admin_perkumpulan_list'})
              })
              .finally(() => {
                _this.loading_submit = false
              })
          } else {
            _this.$http
              .post('/perkumpulan', formData, {
                headers: {
                  'Content-Type': 'multipart/form-data'
                }
              })
              .then((response) => {
                _this.alert('Sukses, Pendaftaran Perkumpulan anda berhasil')
                _this.formatted_data = {...response.data.data, ...response.data.formatted}
                
                _this.resetForm()
                _this.form_step = 1
              })
              .finally(() => {
                _this.loading_submit = false
              })
          }
        }
      },

      validatePemegang () {
        const _this = this
        if (_this.form3.pemegang_tambahan) {
          if (_this.form3.input.pemegang_tambahan.length<=0) {
            this.alert('Mohon isi pengurus lain')
            return false
          }
        } else {
          _this.form3.input.pemegang_tambahan = []
        }
        return true
      }
    },

    metaInfo: {
      title: 'Daftar Perkumpulan'
    }
  }
</script>