                        v-model="form4.input.ktp[index+2]"
                        :rules="[rules.required]"
                        :label="'KTP '+item.kedudukan+' ('+item.nama+')'"
                        hint="File gambar atau pdf"
                        persistent-hint
                        show-size
                        accept="image/*,application/pdf" 
                        class="required"
                      />
                      <attachment-thumbnail
                        :file="form4.ktp_file[index+2]"
                        :ext="form4.ktp_ext[index+2]"
                        :option="{
                            badan: 'pt',
                            id: id,
                            nama_badan: form1.input.nama_perusahaan,
                            type: 'ktp',
                            path: form4.ktp_path[index+2],
                            nama: item.nama,
                            kedudukan: item.kedudukan
                          }"
                      />
                    </v-col>

                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-file-input
                        v-model="form4.input.npwp[index+2]"
                        :label="'NPWP '+item.kedudukan+' ('+item.nama+')'"
                        hint="File gambar atau pdf"
                        persistent-hint
                        show-size
                        accept="image/*,application/pdf" 
                      />
                      <attachment-thumbnail
                        :file="form4.npwp_file[index+2]"
                        :ext="form4.npwp_ext[index+2]"
                        :option="{
                            badan: 'pt',
                            id: id,
                            nama_badan: form1.input.nama_perusahaan,
                            type: 'npwp',
                            path: form4.npwp_path[index+2],
                            nama: item.nama,
                            kedudukan: item.kedudukan
                          }"
                      />
                    </v-col>
                  </v-row>

                  <v-row
                    v-for="(item, index) in form3.input.pemegang_lain"
                    :key="'lain-'+index"
                  >
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-file-input
                        v-model="form4.input.ktp[index+2+form3.input.pemegang_tambahan.length]"
                        :rules="[rules.required]"
                        :label="'KTP '+item.kedudukan+' ('+item.nama+')'"
                        hint="File gambar atau pdf"
                        persistent-hint
                        show-size
                        accept="image/*,application/pdf" 
                        class="required"
                      />
                      <attachment-thumbnail
                        :file="form4.ktp_file[index+2+form3.input.pemegang_tambahan.length]"
                        :ext="form4.ktp_ext[index+2+form3.input.pemegang_tambahan.length]"
                        :option="{
                            badan: 'pt',
                            id: id,
                            nama_badan: form1.input.nama_perusahaan,
                            type: 'ktp',
                            path: form4.ktp_path[index+2+form3.input.pemegang_tambahan.length],
                            nama: item.nama,
                            kedudukan: item.kedudukan
                          }"
                      />
                    </v-col>

                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-file-input
                        v-model="form4.input.npwp[index+2+form3.input.pemegang_tambahan.length]"
                        :label="'NPWP '+item.kedudukan+' ('+item.nama+')'"
                        hint="File gambar atau pdf"
                        persistent-hint
                        show-size
                        accept="image/*,application/pdf" 
                      />
                      <attachment-thumbnail
                        :file="form4.npwp_file[index+2+form3.input.pemegang_tambahan.length]"
                        :ext="form4.npwp_ext[index+2+form3.input.pemegang_tambahan.length]"
                        :option="{
                            badan: 'pt',
                            id: id,
                            nama_badan: form1.input.nama_perusahaan,
                            type: 'npwp',
                            path: form4.npwp_path[index+2+form3.input.pemegang_tambahan.length],
                            nama: item.nama,
                            kedudukan: item.kedudukan
                          }"
                      />
                    </v-col>
                  </v-row>

                  <v-row style="margin-top:200px;">
                    <v-col
                      cols="12"
                    >
                      <v-checkbox
                        v-model="form4.setuju"
                        :rules="[v => !!v || 'Anda harus menyatakan kebenaran data']"
                        label="Dengan ini Saya menyatakan semua data yang Saya masukan adalah benar"
                      />
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>

              <v-btn
                color="primary"
                :loading="loading_submit"
                @click="submit"
              >
                Kirim
              </v-btn>

              <v-btn
                text
                @click="form_step = 3"
              >Kembali</v-btn>
            </v-stepper-content>
          </v-stepper-items>

          <v-alert
            text
            color="info"
            class="ma-0"
          >
            <span style="color:red;">*</span> Wajib diisi
          </v-alert>
              
        </v-stepper>
      </v-col>
    </v-row>

    <submitted-dialog
      v-model="submitted_dialog"
      :data="formatted_data"
    />
  </v-container>
</template>

<script>
  import InputMoney from '@/components/InputMoney';
  import InputCellphone from '@/components/InputCellphone';
  import SubmittedDialog from '@/components/SubmittedDialog';
  import AttachmentThumbnail from '@/components/AttachmentThumbnail';
  import numeral from '@/plugins/numeral'

  export default {
    components: { AttachmentThumbnail, InputMoney, InputCellphone, SubmittedDialog },
    data: () => ({
      biaya_data: {
        "1": {
          normal: 0,
          khusus: 0,
        },
        "2": {
          normal: 0,
          khusus: 0,
        },
        "3": {
          normal: 0,
          khusus: 0,
        },
      },
      form_step: 1,
      form1: {
        input: {
          kategori_modal: "1",
          modal_dasar: 0,
          modal_ditempatkan: 0,
          nama_perusahaan: '',
          nama_alternatif: [''],
        },
        kategori_modal: {
          "1": 1000000000,
          "2": 10000000000,
          "3": 10000000001,
        },
        valid: false
      },
      form2: {
        bidangs: [],
        bidangSearchInput: '',
        input: {
          alamat: "",
          provinsi: '',
          kotkab: '',
          kecamatan: '',
          kelurahan: '',
          rt: '',
          rw: '',
          kodepos: '',
          kode_telpon: '',
          nomor_telpon: '',
          nomor_handphone: '',
          email: '',
          bidangs: [],
        },
        loading_bidang: true,
        loading_regencies: false,
        loading_provinces: true,
        loading_districts: false,
        loading_urbans: false,
        max_bidang: 20,
        regencies: [],
        provinces: [],
        districts: [],
        urbans: [],
        valid: false
      },
      form3: {
        input: {
          direktur_utama: '',
          komisaris_utama: '',
          pemegang_lain: [],
          pemegang_tambahan: [],
          saham_direktur_utama: "0",
          saham_komisaris_utama: "0",
        },
        pemegang_tambahan: false,
        pemegang_lain:false,
        table: {
          defaultItem: {
            nama: '',
            kedudukan: '',
            saham: 0
          },
          dialog: false,
          editedItem: {
            nama: '',
            kedudukan: '',
            saham: 0
          },
          headers: [
            {
              text: 'Nama Lengkap',
              align: 'left',
              sortable: false,
              value: 'nama'
            },
            { text: 'Kedudukan', sortable: false, value: 'kedudukan'},
            { text: 'Saham', sortable: false, value:'saham'},
            { text: '', sortable: false, value:'action'}
          ],
          index: -1,
          which: 1
        },
        valid: false,
      },
      form4: {
        input: {
          ktp: [],
          npwp: []
        },
        ktp_path: [],
        ktp_ext: [],
        ktp_file: [],
        npwp_path: [],
        npwp_ext: [],
        npwp_file: [],
        setuju: false,
        valid: false,
      },
      form1_default: {
        kategori_modal: "1",
        modal_dasar: 0,
        modal_ditempatkan: 0,
        nama_perusahaan: '',
        nama_alternatif: [''],
      },
      form2_default: {
        alamat: "",
        provinsi: '',
        kotkab: '',
        kecamatan: '',
        kelurahan: '',
        rt: '',
        rw: '',
        kodepos: '',
        kode_telpon: '',
        nomor_telpon: '',
        nomor_handphone: '',
        email: '',
        bidangs: [],
      },
      form3_default: {
        direktur_utama: '',
        komisaris_utama: '',
        pemegang_lain: [],
        pemegang_tambahan: [],
        saham_direktur_utama: "0",
        saham_komisaris_utama: "0",
      },
      form4_default: {
        ktp: [],
        npwp: []
      },
      formatted_data: {},
      id: null,
      loading_submit: false,
      rules: {
        minimum3words: v => (!v || (!!v && v.split(' ').length >= 3)) || 'Minimal tiga kata',
        email: v => (/.+@.+/.test(v)) || 'Email tidak valid',
        required: v => !!v || 'Harus diisi',
        arrayRequired: v => (v.length > 0) || 'Harus diisi',
        max(maxNum) {
          return v => (v || '').length <= maxNum || 'Maksimal '+maxNum+' karakter'
        },
        min(minNum) {
          return v => (v || '').length >= minNum || 'Minimum '+minNum+' karakter'
        },
        maximalTotalSaham: null
      },
      submitted_dialog: false,
      view: ''
    }),

    computed: {
      biaya () {
        return this.biaya_data[this.form1.input.kategori_modal]
      },
      formTitle () {
        return this.form3.table.index === -1 ? 'Tambah' : 'Ubah'
      },
      modal_dasar_val () {
        return this.numeralVal(this.form1.input.modal_dasar)
      },
      modal_ditempatkan_val () {
        return this.numeralVal(this.form1.input.modal_ditempatkan)
      },
      rule_modal_dasar () {
        let rules = [this.rules.required]
        if (this.form1.input.kategori_modal == '1') {
          rules.push(() => {
            return (this.modal_dasar_val > 0 && this.modal_dasar_val<=this.form1.kategori_modal[this.form1.input.kategori_modal]) || 's/d 1 Milyar'
          })
        } else if (this.form1.input.kategori_modal == '2') {
          rules.push(() => {
            return (this.modal_dasar_val > this.form1.kategori_modal['1'] && this.modal_dasar_val<=this.form1.kategori_modal[this.form1.input.kategori_modal]) || 's/d 10 Milyar'
          })
        } else if (this.form1.input.kategori_modal == '3'){
          rules.push(() => {
            return (this.modal_dasar_val >= this.form1.kategori_modal["3"]) || 'Lebih dari 10 Milyar'
          })
        } else {
          rules.push(() => true)
        }
        return rules
      },
      rule_modal_ditempatkan () {
        const _25persen = this.modal_dasar_val * 25 / 100
        
        return () => {
          return (this.modal_ditempatkan_val>=_25persen && this.modal_ditempatkan_val<= this.modal_dasar_val) || 'Minimal 25% & tidak lebih dari modal dasar'
        }
      },
      totalSaham () {
        let totalSaham = (this.numeralVal(this.form3.input.saham_direktur_utama) + this.numeralVal(this.form3.input.saham_komisaris_utama));
        for(let idx in this.form3.input.pemegang_tambahan ){
          totalSaham = totalSaham + this.numeralVal(this.form3.input.pemegang_tambahan[idx].saham)
        }
        for(let idx in this.form3.input.pemegang_lain ){
          totalSaham = totalSaham + this.numeralVal(this.form3.input.pemegang_lain[idx].saham)
        }

        return totalSaham
      },
      sahamBelumSesuai () {
        return this.totalSaham != this.modal_dasar_val
      }
    },

    watch: {
      form_step (val) {
        if (val == 2) {
          this.loadProvinces()
          this.loadBidang()
        } else if (val==4 && this.id ) {
          this.loadAttachments()
        }
      },
      '$route' (/* to, from */) {
        if (this.id){
          this.form_step = 1
          this.resetForm()
        }
      },
      'form1.input.kategori_modal': function() {
        this.$nextTick(()=>{
          this.$refs.ref_modal_dasar.validate()
        })
      },
      'form1.input.modal_dasar': function() {
        this.$nextTick(()=>{
          this.$refs.ref_modal_ditempatkan.validate()
        })
      }
    },

    async created () {
      this.rules.maximalTotalSaham = () => ((!this.sahamBelumSesuai) || 'Total saham belum sama dengan modal dasar')
      this.loadHarga()

      if (this.$route.params.pt_id) {
        this.id = this.$route.params.pt_id
        const response = await this.httpGet('/pt/'+this.id, {}, false)
        this.initForm(response.data)
      }
    },

    methods: {
      addAlternatif (index) {
        if (this.form1.input.nama_alternatif[index] && this.form1.input.nama_alternatif[index].split(' ').length >= 3) {
          this.form1.input.nama_alternatif.push('')
        } else {
          this.alert('Nama minimal 3 kata')
        }
      },

      bidangSearchFunc(item, queryText/* , itemText */) {
        queryText = queryText.toLowerCase()
        const nama = String(item.nama).toLowerCase()
        const kode = String(item.kode).toLowerCase()
        const deskripsi = String(item.deskripsi).toLowerCase()
        return (nama.includes(queryText) || kode.includes(queryText)|| deskripsi.includes(queryText))
      },

      close () {
        this.form3.table.dialog = false
        setTimeout(() => {
          this.form3.table.editedItem = Object.assign({}, this.form3.table.defaultItem)
          this.form3.table.index = -1
        }, 300)
      },

      deleteItem (which, item) {
        let theTable = this.form3.input.pemegang_tambahan
        if (which != 1) {
          theTable = this.form3.input.pemegang_lain
        }
        const index = theTable.indexOf(item)
        theTable.splice(index, 1)
      },

      focus_kodepos () {
        const _this = this
        _this.$nextTick(function () {
          _this.$refs.kodepos.focus();
        });
      },

      httpGet(url, data, cache) {
        const _this = this;
        const options = { params: data, cache: true };
        if (cache === false || this.$route.query.new) {
          options.forceUpdate = true;
        }

        return _this.$http
          .get(url, options)
          .then((response) => {
            return response.data;
          });
      },

      initBidangSelect () {
        const _this = this;
        let bidangList = []
        let groups = {}

        for(let idx in _this.form2.bidangs) {
          let currentBidang = _this.form2.bidangs[idx]
          if (!groups[currentBidang.kategori]) {
            groups[currentBidang.kategori] = []
            if (bidangList.length > 0) {
              bidangList.push({ divider: true })
            }
            bidangList.push({ header: currentBidang.kategori+' - '+ currentBidang.nama_kategori})
          }
          
          bidangList.push(currentBidang)
        }
        
        return bidangList
      },

      initForm (data) {
        this.view = data.pt.nama_perusahaan
        this.form1.input.nama_perusahaan = data.pt.nama_perusahaan
        if (data.pt.nama_alternatif_1) {
          this.form1.input.nama_alternatif[0] = data.pt.nama_alternatif_1
        }
        if (data.pt.nama_alternatif_2) {
          this.form1.input.nama_alternatif[1] = data.pt.nama_alternatif_2
        }
        if (data.pt.nama_alternatif_3) {
          this.form1.input.nama_alternatif[2] = data.pt.nama_alternatif_3
        }
        
        this.form1.input.kategori_modal = data.pt.kategori_modal
        this.form1.input.modal_dasar = numeral(data.pt.modal_dasar).format()
        this.form1.input.modal_ditempatkan = numeral(data.pt.modal_ditempatkan).format()

        //FORM 2
        this.form2.input.alamat = data.pt.alamat
        this.form2.provinces = [{
          id: data.pt.provinsi,
          name: data.pt.nama_provinsi
        }]
        this.form2.input.provinsi = data.pt.provinsi

        this.form2.regencies = [{
          id: data.pt.kotkab,
          name: data.pt.nama_kotkab
        }]
        this.form2.input.kotkab = data.pt.kotkab

        this.form2.districts = [{
          id: data.pt.kecamatan,
          name: data.pt.nama_kecamatan
        }]
        this.form2.input.kecamatan = data.pt.kecamatan

        this.form2.urbans = [{
          id: data.pt.kelurahan,
          name: data.pt.nama_kelurahan
        }]
        this.form2.input.kelurahan = data.pt.kelurahan

        this.form2.input.rt = ''+data.pt.rt
        this.form2.input.rw = ''+data.pt.rw
        this.form2.input.kodepos = data.pt.kodepos
        this.form2.input.kode_telpon = data.pt.kode_telpon || ''
        this.form2.input.nomor_telpon = data.pt.nomor_telpon || ''
        this.form2.input.nomor_handphone = data.pt.nomor_handphone
        this.form2.input.email = data.pt.email

        this.form2.input.bidangs = []
        for(let idx in data.pt_bidang) {
          this.form2.input.bidangs.push(data.pt_bidang[idx].bidang)
        }

        //FORM 3 & FORM 4
        this.form4.ktp_path = []
        this.form4.npwp_path = []
        this.form3.input.direktur_utama = data.pt_pemegang[0].nama
        this.form3.input.saham_direktur_utama = numeral(data.pt_pemegang[0].saham).format()
        this.form4.ktp_path.push(data.pt_pemegang[0].ktp)
        this.form4.npwp_path.push(data.pt_pemegang[0].npwp)

        this.form3.input.komisaris_utama = data.pt_pemegang[1].nama
        this.form3.input.saham_komisaris_utama = numeral(data.pt_pemegang[1].saham).format()
        this.form4.ktp_path.push(data.pt_pemegang[1].ktp)
        if (data.pt_pemegang[1].npwp){
          this.form4.npwp_path.push(data.pt_pemegang[1].npwp)
        }

        this.form3.input.pemegang_tambahan = []
        for(let idx=2 ; idx < data.pt_pemegang.length ; idx++) {
          data.pt_pemegang[idx].saham = numeral(data.pt_pemegang[idx].saham).format()
          if (data.pt_pemegang[idx].tipe == '2') {
            this.form3.input.pemegang_tambahan.push(data.pt_pemegang[idx])
            this.form4.ktp_path.push(data.pt_pemegang[idx].ktp)
            if (data.pt_pemegang[idx].npwp){
              this.form4.npwp_path[idx] = data.pt_pemegang[idx].npwp
            }
            this.form3.pemegang_tambahan = true
          }

        }

        this.form3.input.pemegang_lain = []
        for(let idx=2 ; idx < data.pt_pemegang.length ; idx++) {
          if (data.pt_pemegang[idx].tipe == '3') {
            this.form3.input.pemegang_lain.push(data.pt_pemegang[idx])
            this.form4.ktp_path.push(data.pt_pemegang[idx].ktp)
            if (data.pt_pemegang[idx].npwp){
              this.form4.npwp_path[idx] = data.pt_pemegang[idx].npwp
            }
            this.form3.pemegang_lain = true
          }
        }

      },

      loadAttachments () {
        const _this = this
        _this.form4.ktp_ext = []
        _this.form4.ktp_file = []
        _this.form4.npwp_ext = []
        _this.form4.npwp_file = []

        for(let idx in _this.form4.ktp_path) {
          _this.$http
            .get('/pt/'+_this.id+'/file', {
              cache: true,
              headers: {
                'Accept': '*/*'
              },
              params: {path: _this.form4.ktp_path[idx] }, 
              responseType: 'blob'
            })
            .then(async (res) => {
              const blobObject = await _this.blobToObject(res.data)
              _this.form4.ktp_ext.push(blobObject.fileType.ext)
              _this.form4.ktp_file.push(blobObject.url)
            })
            .catch((err) => {
              _this.$log.debug(err)
            });
        }

        for(let idx in _this.form4.npwp_path) {
          _this.$http
            .get('/pt/'+_this.id+'/file', {
              cache: true,
              headers: {
                'Accept': '*/*'
              },
              params: {path: _this.form4.npwp_path[idx] }, 
              responseType: 'blob'
            })
            .then(async (res) => {
              const blobObject = await _this.blobToObject(res.data)
              _this.$set(_this.form4.npwp_ext, idx, blobObject.fileType.ext)
              _this.$set(_this.form4.npwp_file, idx, blobObject.url)
            })
            .catch((err) => {
              _this.$log.debug(err)
            });
        }
      },

      loadBidang () {
        const _this = this;
        
        _this.form2.loading_bidang = true
        _this.httpGet('/bidang')
          .then((response) => {
            _this.form2.bidangs = response.data
            _this.form2.bidangs = _this.initBidangSelect()
          })
          .finally(() => {
            _this.form2.loading_bidang = false
          })
      },

      loadHarga () {
        const _this = this

        _this.$http
          .get('/harga/pt')
          .then((response) => {
            const dataHarga = [...response.data.data]
            _this.biaya_data["1"] = dataHarga[0];
            _this.biaya_data["2"] = dataHarga[1];
            _this.biaya_data["3"] = dataHarga[2];
          });
      },

      loadProvinces () {
        const _this = this;
        
        _this.form2.loading_provinces = true
        _this.httpGet('/provinces')
          .then((response) => {
            _this.form2.provinces = response.data
          })
          .finally(() => {
            _this.form2.loading_provinces = false
          })
      },

      loadRegencies () {
        const _this = this;
        
        _this.form2.loading_regencies = true
        _this.httpGet('/regencies?province_id='+_this.form2.input.provinsi)
          .then((response) => {
            _this.form2.regencies = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kotkab.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_regencies = false
          })
      },

      loadDistricts () {
        const _this = this;
        
        _this.form2.loading_districts = true
        _this.httpGet('/districts?regency_id=' + _this.form2.input.kotkab)
          .then((response) => {
            _this.form2.districts = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kecamatan.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_districts = false
          })
      },

      loadUrbans () {
        const _this = this;
        
        _this.form2.loading_urbans = true
        _this.httpGet('/urbans?district_id=' + _this.form2.input.kecamatan)
          .then((response) => {
            _this.form2.urbans = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kelurahan.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_urbans = false
          })
      },

      nextStep(theFormRef) {
        if (theFormRef.validate()) this.form_step++
      },

      removeAlternatif () {
        this.form1.input.nama_alternatif.pop()
      },

      removeSelectedBidang (item) {
        const index = this.form2.input.bidangs.indexOf(item)
        if (index >= -1) this.form2.input.bidangs.splice(index, 1)
      },

      resetForm () {
        const _this = this

        _this.id = null
        _this.view = null
        _this.$refs.form1.resetValidation()
        _this.$refs.form2.resetValidation()
        _this.$refs.form3.resetValidation()
        _this.$refs.form4.resetValidation()

        _this.form1.input = {... _this.form1_default}
        _this.form2.input = {... _this.form2_default}
        _this.form3.input = {... _this.form3_default}
        _this.form4.input = {... _this.form4_default}
        _this.form3.pemegang_tambahan = false
        _this.form3.pemegang_lain = false

        _this.form4.ktp_path = []
        _this.form4.ktp_ext = []
        _this.form4.ktp_file = []
        _this.form4.npwp_path = []
        _this.form4.npwp_ext = []
        _this.form4.npwp_file = []
        _this.form4.setuju = false
      },

      save () {
        let theTable = this.form3.input.pemegang_tambahan
        if (this.form3.table.which != 1) {
          theTable = this.form3.input.pemegang_lain
          this.form3.table.editedItem.kedudukan = 'Pemegang Saham'
        }

        if(!this.form3.table.editedItem.nama || !this.form3.table.editedItem.kedudukan || this.numeralVal(this.form3.table.editedItem.saham)<0) {
          this.alert("Mohon isi semua kolom")
          return false
        }

        if (this.form3.table.index > -1) {
          Object.assign(theTable[this.form3.table.index], this.form3.table.editedItem)
        } else {
          theTable.push(this.form3.table.editedItem)
        }

        this.$refs.form3.resetValidation()
        this.close()
      },

      selectedBidangUpdated () {
        // this.form2.disabledBidang = this.form2.input.bidangs.length==this.form2.max_bidang
        this.form2.bidangSearchInput = ''
        this.$refs.ref_autocomplete_bidang.isMenuActive = false
      },

      showFormDialog(which, item) {
        this.form3.table.which = which

        if (item) {
          if (which == 1) {
            this.form3.table.index = this.form3.input.pemegang_tambahan.indexOf(item)
          } else {
            this.form3.table.index = this.form3.input.pemegang_lain.indexOf(item)
          }
          this.form3.table.editedItem = Object.assign({}, item)
        }
        this.form3.table.dialog = true
      },
      
      submit () {
        const _this = this

        if (_this.$refs.form4.validate()) {
          const postData = {..._this.form1.input, ..._this.form2.input, ..._this.form3.input, ..._this.form4.input}

          const formData = new FormData();
          formData.append('biaya', _this.biaya.khusus)
          
          for ( var key in postData ) {
            formData.append(key, postData[key]);
          }

          formData.delete('nama_alternatif')
          for(let idx in postData.nama_alternatif) {
            formData.append('nama_alternatif[]', postData.nama_alternatif[idx])
          }

          formData.delete('modal_dasar')
          formData.set("modal_dasar", this.modal_dasar_val)
          formData.delete('modal_ditempatkan')
          formData.set("modal_ditempatkan", this.modal_ditempatkan_val)
          
          formData.delete('bidangs')
          for(let idx in postData.bidangs) {
            formData.append('bidangs[]', postData.bidangs[idx])
          }

          formData.delete('ktp')
          for(let idx in _this.form4.input.ktp) {
            formData.append('ktp['+idx+']', postData.ktp[idx])
          }

          formData.delete('npwp')
          for(let idx in _this.form4.input.npwp) {
            formData.append('npwp['+idx+']', postData.npwp[idx])
          }
          
          formData.delete('saham_direktur_utama')
          formData.delete('saham_komisaris_utama')
          formData.set('saham_direktur_utama', this.numeralVal(postData.saham_direktur_utama))
          formData.set('saham_komisaris_utama', this.numeralVal(postData.saham_komisaris_utama))

          formData.delete('pemegang_tambahan')
          formData.delete('pemegang_lain')
          for(let idx in postData.pemegang_tambahan ){
            postData.pemegang_tambahan[idx].saham = this.numeralVal(postData.pemegang_tambahan[idx].saham)
          }
          for(let idx in postData.pemegang_lain ){
            postData.pemegang_lain[idx].saham = this.numeralVal(postData.pemegang_lain[idx].saham)
          }
          formData.set("pemegang_lain", JSON.stringify(postData.pemegang_lain))
          formData.set("pemegang_tambahan", JSON.stringify(postData.pemegang_tambahan))
          
          _this.loading_submit = true
          _this.submitted_dialog = true

          if (_this.id) {
            _this.$http
              .post('/pt/'+_this.id, formData, {
                headers: {
                  'Content-Type': 'multipart/form-data'
                }
              })
              .then(() => {
                _this.alert('Sukses, perubahan data PT berhasil')
                
                _this.$router.push({name:'admin_pt_list'})
              })
              .finally(() => {
                _this.loading_submit = false
              })
          } else {
            _this.$http
              .post('/pt', formData, {
                headers: {
                  'Content-Type': 'multipart/form-data'
                }
              })
              .then((response) => {
                _this.alert('Sukses, Pendaftaran PT anda berhasil')
                _this.formatted_data = {...response.data.data, ...response.data.formatted}
                
                _this.resetForm()

                _this.form_step = 1
              })
              .finally(() => {
                _this.loading_submit = false
              })
          }

        }
      },

      validatePemegang () {
        const _this = this

        if (_this.sahamBelumSesuai) {
          this.alert('Harap sesuaikan jumlah saham')
          return false
        }

        if (_this.form3.pemegang_tambahan) {
          if (_this.form3.input.pemegang_tambahan.length<=0) {
            this.alert('Mohon isi direktur/komisaris lain')
            return false
          }
        } else {
          _this.form3.input.pemegang_tambahan = []
        }

        if (_this.form3.pemegang_lain) {
          if (_this.form3.input.pemegang_lain.length<=0) {
            this.alert('Mohon isi pemegang saham lain')
            return false
          }
        } else {
          _this.form3.input.pemegang_lain = []
        }
        return true
      }
    },

    metaInfo: {
      title: 'Daftar PT'
    }
  }
</script>