export const snackbar = {
  namespaced: true,
  state: {
    text: '',
    priority: 1, // 1:low, 2:medium, 3:high
  },
  getters: {
    GET_ALERT(state) {
      return { priority: state.priority, text: state.text };
      // return state.text;
    },
  },
  mutations: {
    SET_ALERT(state, payload) {
      state.text = payload;
    },
    SET_PRIORITY(state, payload) {
      state.priority = payload;
    },
  },
  actions: {
    SET_ALERT(context, payload) {
      context.commit('SET_ALERT', payload);
    },
    RESET_ALERT(context) {
      context.commit('SET_ALERT', '');
      context.commit('SET_PRIORITY', 1);
    },
    catchBroadcast({ commit }, message) {
      commit('SET_ALERT', message);
      commit('SET_PRIORITY', 3);
    },
  },
};
