      formatted_data: {},
      id: null,
      loading_submit: false,
      rules: {
        minimum3words: v => (!v || (!!v && v.split(' ').length >= 3)) || 'Minimal tiga kata',
        email: v => (/.+@.+/.test(v)) || 'Email tidak valid',
        required: v => !!v || 'Harus diisi',
        arrayRequired: v => (v.length > 0) || 'Harus diisi',
        max(maxNum) {
          return v => (v || '').length <= maxNum || 'Maksimal '+maxNum+' karakter'
        },
        min(minNum) {
          return v => (v || '').length >= minNum || 'Minimum '+minNum+' karakter'
        }
      },
      submitted_dialog: false,
      view: ''
    }),

    computed: {
      alokasi_val() {
        return this.numeralVal(this.form1.input.alokasi)
      },

      biaya () {
        return this.biaya_data[this.form1.input.kategori_modal]
      },

      formTitle () {
        return this.form3.table.index === -1 ? 'Tambah' : 'Ubah'
      },

      modal_dasar_val () {
        return this.numeralVal(this.form1.input.modal_dasar)
      },

      rule_alokasi () {
        const rules = [this.rules.required, () => (this.alokasi_val>=15000000 || 'Minimal 15 juta')]
        rules.push(() => this.alokasi_val < this.modal_dasar_val || 'Harus di bawah modal dasar')

        return rules
      },

      rule_modal_dasar () {
        const theForm = this.form1
        const theFormInput = this.form1.input
        let rules = [this.rules.required]

        //if simpan pinjam
        if (theFormInput.jenis == '1') {
          rules.push( () => (this.modal_dasar_val>=theForm.wilayah_modal[theFormInput.wilayah]) || 'Minimal '+numeral(theForm.wilayah_modal[theFormInput.wilayah]).format()  )
        } else if (theFormInput.unit_simpan_pinjam) {
          rules.push(() => (this.modal_dasar_val>15000000) || 'Harus di atas 15 juta')
          rules.push(() => this.modal_dasar_val > this.alokasi_val || 'Harus di atas alokasi')
        }

        if (this.form1.input.kategori_modal == '1') {
          rules.push(() => {
            return (this.modal_dasar_val > 0 && this.modal_dasar_val<=this.form1.kategori_modal[this.form1.input.kategori_modal]) || 's/d 1 Milyar'
          })
        } else if (this.form1.input.kategori_modal == '2') {
          rules.push(() => {
            return (this.modal_dasar_val > this.form1.kategori_modal['1'] && this.modal_dasar_val<=this.form1.kategori_modal[this.form1.input.kategori_modal]) || 's/d 10 Milyar'
          })
        } else if (this.form1.input.kategori_modal == '3'){
          rules.push(() => {
            return (this.modal_dasar_val >= this.form1.kategori_modal["3"]) || 'Lebih dari 10 Milyar'
          })
        } else {
          rules.push(() => true)
        }
        return rules
      },
    },

    watch: {
      form_step (val) {
        if (val == 2) {
          this.loadProvinces()
          this.loadBidang()
        } else if (val==4 && this.id ) {
          this.loadAttachments()
        }
      },
      '$route' (/* to, from */) {
        if (this.id){
          this.form_step = 1
          this.resetForm()
        }
      },

      'form1.input.kategori_modal': function() {
        this.$nextTick(()=>{
          this.$refs.ref_modal_dasar.validate()
        })
      },

      'form1.input.modal_dasar' : function() {
        this.$refs.ref_alokasi && this.$refs.ref_alokasi.validate()
      },

      'form1.input.wilayah' : function() {
        this.$refs.ref_modal_dasar.validate()
      }
    },

    async created () {
      this.loadHarga()

      if (this.$route.params.koperasi_id) {
        this.id = this.$route.params.koperasi_id
        const response = await this.httpGet('/koperasi/'+this.id, {}, false)
        this.initForm(response.data)
      }
    },

    methods: {
      addAlternatif (index) {
        if (this.form1.input.nama_alternatif[index] && this.form1.input.nama_alternatif[index].split(' ').length >= 3) {
          this.form1.input.nama_alternatif.push('')
        } else {
          this.alert('Nama minimal 3 kata')
        }
      },

      bidangSearchFunc(item, queryText/* , itemText */) {
        queryText = queryText.toLowerCase()
        const nama = String(item.nama).toLowerCase()
        const kode = String(item.kode).toLowerCase()
        const deskripsi = String(item.deskripsi).toLowerCase()
        return (nama.includes(queryText) || kode.includes(queryText)|| deskripsi.includes(queryText))
      },

      close () {
        this.form3.table.dialog = false
        setTimeout(() => {
          this.form3.table.editedItem = Object.assign({}, this.form3.table.defaultItem)
          this.form3.table.index = -1
        }, 300)
      },

      deleteItem (item) {
        let theTable = this.form3.input.pemegang_tambahan
        const index = theTable.indexOf(item)
        theTable.splice(index, 1)
      },

      focus_kodepos () {
        const _this = this
        _this.$nextTick(function () {
          _this.$refs.kodepos.focus();
        });
      },
      
      gantiJenis (newVal) {
        if (newVal == '1') {
          this.form1.input.unit_simpan_pinjam = false
          this.form1.input.alokasi = "0"
        }
      },

      gantiUnit (newVal) {
        if (!newVal) {
          this.form1.input.alokasi = "0"
        }
      },

      httpGet(url, data, cache) {
        const _this = this;
        const options = { params: data, cache: true };
        if (cache === false || this.$route.query.new) {
          options.forceUpdate = true;
        }

        return _this.$http
          .get(url, options)
          .then((response) => {
            return response.data;
          });
      },

      initBidangSelect () {
        const _this = this;
        let bidangList = []
        let groups = {}

        for(let idx in _this.form2.bidangs) {
          let currentBidang = _this.form2.bidangs[idx]
          if (!groups[currentBidang.kategori]) {
            groups[currentBidang.kategori] = []
            if (bidangList.length > 0) {
              bidangList.push({ divider: true })
            }
            bidangList.push({ header: currentBidang.kategori+' - '+ currentBidang.nama_kategori})
          }
          
          bidangList.push(currentBidang)
        }
        
        return bidangList
      },

      initForm (data) {
        this.view = data.koperasi.nama_koperasi
        this.form1.input.nama_koperasi = data.koperasi.nama_koperasi
        if (data.koperasi.nama_alternatif_1) {
          this.form1.input.nama_alternatif[0] = data.koperasi.nama_alternatif_1
        }
        if (data.koperasi.nama_alternatif_2) {
          this.form1.input.nama_alternatif[1] = data.koperasi.nama_alternatif_2
        }
        if (data.koperasi.nama_alternatif_3) {
          this.form1.input.nama_alternatif[2] = data.koperasi.nama_alternatif_3
        }
        this.form1.input.wilayah = data.koperasi.wilayah
        this.form1.input.jenis = data.koperasi.jenis
        this.form1.input.kategori_modal = data.koperasi.kategori_modal
        this.form1.input.modal_dasar = numeral(data.koperasi.modal_dasar).format()
        this.form1.input.wajib = numeral(data.koperasi.wajib).format()
        this.form1.input.pokok = numeral(data.koperasi.pokok).format()
        this.form1.input.sukarela = numeral(data.koperasi.sukarela).format()
        this.form1.input.unit_simpan_pinjam = data.koperasi.unit_simpan_pinjam == 'true'
        this.form1.input.alokasi = numeral(data.koperasi.alokasi).format()

        //FORM 2
        this.form2.input.alamat = data.koperasi.alamat
        this.form2.provinces = [{
          id: data.koperasi.provinsi,
          name: data.koperasi.nama_provinsi
        }]
        this.form2.input.provinsi = data.koperasi.provinsi

        this.form2.regencies = [{
          id: data.koperasi.kotkab,
          name: data.koperasi.nama_kotkab
        }]
        this.form2.input.kotkab = data.koperasi.kotkab

        this.form2.districts = [{
          id: data.koperasi.kecamatan,
          name: data.koperasi.nama_kecamatan
        }]
        this.form2.input.kecamatan = data.koperasi.kecamatan

        this.form2.urbans = [{
          id: data.koperasi.kelurahan,
          name: data.koperasi.nama_kelurahan
        }]
        this.form2.input.kelurahan = data.koperasi.kelurahan

        this.form2.input.rt = ''+data.koperasi.rt
        this.form2.input.rw = ''+data.koperasi.rw
        this.form2.input.kodepos = data.koperasi.kodepos
        this.form2.input.kode_telpon = data.koperasi.kode_telpon || ''
        this.form2.input.nomor_telpon = data.koperasi.nomor_telpon || ''
        this.form2.input.nomor_handphone = data.koperasi.nomor_handphone
        this.form2.input.email = data.koperasi.email

        this.form2.input.bidangs = []
        for(let idx in data.koperasi_bidang) {
          this.form2.input.bidangs.push(data.koperasi_bidang[idx].bidang)
        }

        //FORM 3 & 4
        this.form3.input.jumlah_anggota = data.koperasi.jumlah_anggota

        this.form4.ktp_path = []
        this.form4.npwp_path = []
        this.form3.input.pemegang_utama = []
        for(let idx in data.koperasi_pemegang) {
          if (data.koperasi_pemegang[idx].tipe == '1') {
            this.form3.input.pemegang_utama.push(data.koperasi_pemegang[idx])
            this.form4.ktp_path.push(data.koperasi_pemegang[idx].ktp)
            if (data.koperasi_pemegang[idx].npwp){
              this.form4.npwp_path[idx] = data.koperasi_pemegang[idx].npwp
            }
          }
        }

        this.form3.input.pemegang_tambahan = []
        for(let idx in data.koperasi_pemegang) {
          if (data.koperasi_pemegang[idx].tipe == '2') {
            this.form3.input.pemegang_tambahan.push(data.koperasi_pemegang[idx])
            this.form4.ktp_path.push(data.koperasi_pemegang[idx].ktp)
            if (data.koperasi_pemegang[idx].npwp){
              this.form4.npwp_path[idx] = data.koperasi_pemegang[idx].npwp
            }
            this.form3.pemegang_tambahan = true
          }
        }

        this.form4.ktp_anggota_path = data.koperasi.ktp_anggota
        this.form4.daftar_hadir_path = data.koperasi.daftar_hadir
        this.form4.rekapitulasi_path = data.koperasi.rekapitulasi
        this.form4.berita_pendirian_path = data.koperasi.berita_pendirian
      },

      async loadAttachments () {
        const _this = this
        _this.form4.ktp_ext = []
        _this.form4.ktp_file = []
        _this.form4.npwp_ext = []
        _this.form4.npwp_file = []

        for(let idx in _this.form4.ktp_path) {
          _this.$http
            .get('/koperasi/'+_this.id+'/file', {
              cache: true,
              headers: {
                'Accept': '*/*'
              },
              params: {path: _this.form4.ktp_path[idx] }, 
              responseType: 'blob'
            })
            .then(async (res) => {
              const blobObject = await _this.blobToObject(res.data)
              _this.form4.ktp_ext.push(blobObject.fileType.ext)
              _this.form4.ktp_file.push(blobObject.url)
            })
            .catch((err) => {
              _this.$log.debug(err)
            });
        }

        for(let idx in _this.form4.npwp_path) {
          _this.$http
            .get('/koperasi/'+_this.id+'/file', {
              cache: true,
              headers: {
                'Accept': '*/*'
              },
              params: {path: _this.form4.npwp_path[idx] }, 
              responseType: 'blob'
            })
            .then(async (res) => {
              const blobObject = await _this.blobToObject(res.data)
              _this.$set(_this.form4.npwp_ext, idx, blobObject.fileType.ext)
              _this.$set(_this.form4.npwp_file, idx, blobObject.url)
            })
            .catch((err) => {
              _this.$log.debug(err)
            });
        }

        _this.form4.ktp_anggota_file = await this.loadPDF (_this.id, _this.form4.ktp_anggota_path)
        _this.form4.daftar_hadir_file = await this.loadPDF (_this.id, _this.form4.daftar_hadir_path)
        _this.form4.rekapitulasi_file = await this.loadPDF (_this.id, _this.form4.rekapitulasi_path)
        _this.form4.berita_pendirian_file = await this.loadPDF (_this.id, _this.form4.berita_pendirian_path)
      },

      loadBidang () {
        const _this = this;
        
        _this.form2.loading_bidang = true
        _this.httpGet('/bidang')
          .then((response) => {
            _this.form2.bidangs = response.data
            _this.form2.bidangs = _this.initBidangSelect()
          })
          .finally(() => {
            _this.form2.loading_bidang = false
          })
      },

      loadHarga () {
        const _this = this

        _this.$http
          .get('/harga/koperasi')
          .then((response) => {
            const dataHarga = [...response.data.data]
            _this.biaya_data["1"] = dataHarga[0];
            _this.biaya_data["2"] = dataHarga[1];
            _this.biaya_data["3"] = dataHarga[2];
          });
      },

      async loadPDF (id, path) {
        const _this = this
        return await this.$http
          .get('/koperasi/'+id+'/file', {
            cache: true,
            headers: {
              'Accept': '*/*'
            },
            params: {path: path }, 
            responseType: 'blob'
          })
          .then(async (res) => {
            const blobObject = await _this.blobToObject(res.data)
            return blobObject.url
          })
          .catch((err) => {
            _this.$log.debug(err)
          });
      },

      loadProvinces () {
        const _this = this;
        
        _this.form2.loading_provinces = true
        _this.httpGet('/provinces')
          .then((response) => {
            _this.form2.provinces = response.data
          })
          .finally(() => {
            _this.form2.loading_provinces = false
          })
      },

      loadRegencies () {
        const _this = this;
        
        _this.form2.loading_regencies = true
        _this.httpGet('/regencies?province_id='+_this.form2.input.provinsi)
          .then((response) => {
            _this.form2.regencies = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kotkab.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_regencies = false
          })
      },

      loadDistricts () {
        const _this = this;
        
        _this.form2.loading_districts = true
        _this.httpGet('/districts?regency_id=' + _this.form2.input.kotkab)
          .then((response) => {
            _this.form2.districts = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kecamatan.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_districts = false
          })
      },

      loadUrbans () {
        const _this = this;
        
        _this.form2.loading_urbans = true
        _this.httpGet('/urbans?district_id=' + _this.form2.input.kecamatan)
          .then((response) => {
            _this.form2.urbans = response.data
          })
          .then(() => {
            _this.$nextTick(function () {
              _this.$refs.kelurahan.focus();
            });
          })
          .finally(() => {
            _this.form2.loading_urbans = false
          })
      },

      nextStep(theFormRef) {
        if (theFormRef.validate()) this.form_step++
      },

      removeAlternatif () {
        this.form1.input.nama_alternatif.pop()
      },

      removeSelectedBidang (item) {
        const index = this.form2.input.bidangs.indexOf(item)
        if (index >= -1) this.form2.input.bidangs.splice(index, 1)
      },

      resetForm () {
        const _this = this

        _this.id = null
        _this.view = null

        _this.$refs.form1.resetValidation()
        _this.$refs.form2.resetValidation()
        _this.$refs.form3.resetValidation()
        _this.$refs.form4.resetValidation()

        _this.form1.input = {... _this.form1_default}
        _this.form2.input = {... _this.form2_default}
        _this.form3.input = {... _this.form3_default}
        _this.form4.input = {... _this.form4_default}
        _this.form3.pemegang_tambahan = false

        _this.form4.ktp_path = []
        _this.form4.ktp_ext = []
        _this.form4.ktp_file = []
        _this.form4.npwp_path = []
        _this.form4.npwp_ext = []
        _this.form4.npwp_file = []

        _this.ktp_anggota_path = null
        _this.ktp_anggota_file = null
        _this.daftar_hadir_path = null
        _this.daftar_hadir_file = null
        _this.rekapitulasi_path = null
        _this.rekapitulasi_file = null
        _this.berita_pendirian_path = null
        _this.berita_pendirian_file = null
        _this.form4.setuju = false
      },

      save () {
        let theTable = this.form3.input.pemegang_tambahan
        const theForm = this.form3.table.editedItem

        if(!theForm.nama || !theForm.kedudukan) {
          this.alert("Mohon isi semua kolom")
          return false
        }

        if (this.form3.table.index > -1) {
          Object.assign(theTable[this.form3.table.index], theForm)
        } else {
          theTable.push(theForm)
        }
        this.close()
      },

      selectedBidangUpdated () {
        // this.form2.disabledBidang = this.form2.input.bidangs.length==this.form2.max_bidang
        this.form2.bidangSearchInput = ''
        this.$refs.ref_autocomplete_bidang.isMenuActive = false
      },

      showFormDialog(item)   {
        if (item) {
          this.form3.table.index = this.form3.input.pemegang_tambahan.indexOf(item)
          this.form3.table.editedItem = Object.assign({}, item)
        }
        this.form3.table.dialog = true
      },
      
      submit () {
        const _this = this

        if (_this.$refs.form4.validate()) {
          const postData = {..._this.form1.input, ..._this.form2.input, ..._this.form3.input, ..._this.form4.input}

          const formData = new FormData();
          formData.append('biaya', _this.biaya.khusus)
          
          for ( var key in postData ) {
            formData.append(key, postData[key]);
          }
          
          formData.delete('nama_alternatif')
          for(let idx in postData.nama_alternatif) {
            formData.append('nama_alternatif[]', postData.nama_alternatif[idx])
          }
          formData.delete('alokasi')
          formData.set("alokasi", this.alokasi_val)
          formData.delete('wajib')
          formData.set("wajib", this.numeralVal(postData.wajib))
          formData.delete('pokok')
          formData.set("pokok", this.numeralVal(postData.pokok))
          formData.delete('sukarela')
          formData.set("sukarela", this.numeralVal(postData.sukarela))

          formData.delete('modal_dasar')
          formData.set("modal_dasar", this.modal_dasar_val)
          formData.delete('bidangs')
          for(let idx in postData.bidangs) {
            formData.append('bidangs[]', postData.bidangs[idx])
          }

          formData.delete('ktp')
          for(let idx in postData.ktp) {
            formData.append('ktp['+idx+']', postData.ktp[idx])
          }

          formData.delete('npwp')
          for(let idx in postData.npwp) {
            formData.append('npwp['+idx+']', postData.npwp[idx])
          }
          
          formData.delete('pemegang_utama')
          formData.set("pemegang_utama", JSON.stringify(postData.pemegang_utama))
          formData.delete('pemegang_tambahan')
          formData.set("pemegang_tambahan", JSON.stringify(postData.pemegang_tambahan))
          
          _this.loading_submit = true
          _this.submitted_dialog = true

          if (_this.id) {
            _this.$http
              .post('/koperasi/'+_this.id, formData, {
                headers: {
                  'Content-Type': 'multipart/form-data'
                }
              })
              .then(() => {
                _this.alert('Sukses, perubahan data Koperasi berhasil')
                
                _this.$router.push({name:'admin_koperasi_list'})
              })
              .finally(() => {
                _this.loading_submit = false
              })
          } else {
            _this.$http
              .post('/koperasi', formData, {
                headers: {
                  'Content-Type': 'multipart/form-data'
                }
              })
              .then((response) => {
                _this.alert('Sukses, Pendaftaran Koperasi anda berhasil')
                _this.formatted_data = {...response.data.data, ...response.data.formatted}
                
                _this.resetForm()
                _this.form_step = 1
              })
              .finally(() => {
                _this.loading_submit = false
              })
          }

        }
      },

      validatePemegang () {
        const _this = this
        if (_this.form3.pemegang_tambahan) {
          if (_this.form3.input.pemegang_tambahan.length<=0) {
            this.alert('Mohon isi pengurus lain')
            return false
          }
        } else {
          _this.form3.input.pemegang_tambahan = []
        }
        return true
      }
    },

    metaInfo: {
      title: 'Daftar Koperasi'
    }
  }
</script>