<template>
  <v-container >
    <v-row>
      <v-col
        cols="12"
        xs="12"
      >
        <v-data-table
          :headers="table.headers"
          :items="table.items"
          :items-per-page="-1"
          :loading="table.loading"
          hide-default-footer
          class="elevation-1"
          loading-text="Memuat... Harap Tunggu"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title>Daftar Harga</v-toolbar-title>
              
              <v-divider
                class="mx-4"
                inset
                vertical
              />

              <v-btn 
                v-if="!readOnly"
                color="info"
                fab
                small
                depressed
                @click="loadData(false)"
              >
                <v-icon
                  :class="table.loading ? 'fa-spin' : ''"
                  small
                >fa-redo fa-sm</v-icon>
              </v-btn>
            </v-toolbar>
          </template>

          <template v-slot:item="{ item }">
            <tr :class="table.color[item.tipe]+' lighten-5'">
              <td>{{ item.tipe | uppercase}}</td>
              <td>{{ kategori_text[item.kategori] }}</td>
              <td class="text-sm-right" style="width:150px;"><s>{{ item.normal | numberFormat }}</s></td>
              <td class="text-sm-right" style="width:150px;">{{ item.khusus | numberFormat }}</td>
              <td>
                <v-icon
                  v-if="!readOnly"
                  small
                  @click="editItem(item)"
                >
                  fa-pencil-alt
                </v-icon>
              </td>
            </tr>
          </template>
        </v-data-table>
      </v-col>
    </v-row>

    <v-dialog v-if="!readOnly" v-model="dialog" max-width="700px">
      <v-card v-show="form.loading" >
        <v-card-text>
          Harap Tunggu
          <v-progress-linear
            indeterminate
            color="primary"
            class="mb-0"
          />
        </v-card-text>
      </v-card>

      <v-card v-show="!form.loading">
        <v-card-title>
          <span class="headline">Ubah ({{form.input.tipe | uppercase}})</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" xs="12" sm="8">
                <h3>Kategori : {{ kategori_text[form.input.kategori] }}</h3>
              </v-col>
              <v-col cols="12" xs="12" sm="8">
                <v-text-field
                  v-model="form.input.normal"
                  :rules="[rules.required, rules.max(12)]"
                  ref="normal"
                  maxlength="12"
                  prefix="Rp. "
                  label="Harga Normal"
                  @keydown="numberOnly"
                  @keyup="form.input.normal && (form.input.normal = form.input.normal.replace(/^0+(?=\d)/,''))"
                />
                <p
                  class="font-weight-thin"
                >Rp. {{ form.input.normal | numberFormat }}</p>
              </v-col>
              <v-col cols="12" xs="12" sm="8">
                <v-text-field
                  v-model="form.input.khusus"
                  :rules="[rules.required, rules.max(12)]"
                  maxlength="12"
                  prefix="Rp. "
                  label="Harga Khusus"
                  @keydown="numberOnly"
                  @keyup="form.input.khusus && (form.input.khusus = form.input.khusus.replace(/^0+(?=\d)/,''))"
                />
                <p
                  class="font-weight-thin"
                >Rp. {{ form.input.khusus | numberFormat }}</p>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" text @click="close">Batal</v-btn>
          <v-btn color="blue darken-1" text @click="save">Simpan</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    props: ['readOnly'],
    data () {
      return {
        dialog: false,
        form: {
          default: {
            id: '',
            tipe: '',
            kategori: '',
            normal: '',
            khusus: ''
          },
          input: {
            id: '',
            tipe: '',
            kategori: '',
            normal: '',
            khusus: ''
          },
          loading: false,
          valid: false
        },
        kategori: {
          "1": 1000000000,
          "2": 10000000000,
          "3": 10000000001,
        },
        kategori_text: {
          "1": '<= 1 Miliar',
          "2": '<= 10 Miliar',
          "3": '> 10 Miliar',
        },
        rules: {
          required: v => !!v || 'Harus diisi',
          max(maxNum) {
            return v => (v || '').length <= maxNum || 'Maksimal '+maxNum+' karakter'
          },
          min(minNum) {
            return v => (v || '').length >= minNum || 'Minimum '+minNum+' karakter'
          },
        },
        table: {
          headers: [
            {
              text: 'Badan',
              align: 'left',
              sortable: false,
              value: 'tipe',
            },
            { text: 'Kategori', value: 'kategori', sortable: false },
            { text: 'Harga Normal (Rp.)', align: 'right', value: 'normal', sortable: false },
            { text: 'Harga Khusus (Rp.)', align: 'right', value: 'khusus', sortable: false },
            { text: '', value: 'action', sortable: false },
          ],
          color:{
            "pt": "indigo",
            "cv": "red",
            "koperasi": "teal",
            "firma": "brown",
            "perkumpulan": "lime",
            "yayasan": "amber",
          },
          index: -1,
          items: [],
          loading: true
        }
      }
    },

    created () {
      this.loadData()
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    methods: {
      close () {
        this.dialog = false
        setTimeout(() => {
          this.form.input = Object.assign({}, this.form.default)
          this.table.index = -1
        }, 300)
      },

      editItem (item) {
        this.table.index = this.table.items.indexOf(item)
        this.form.input = Object.assign({}, item)
        this.showForm()
      },

      httpGet(url, data, cache) {
        const _this = this;
        const options = { params: data, cache: true };
        if (cache === false || this.$route.query.new) {
          options.forceUpdate = true;
        }

        return _this.$http
          .get(url, options)
          .then((response) => {
            return response.data;
          });
      },

      loadData (cache) {
        const _this = this;
        
        _this.table.loading = true
        _this.httpGet('/harga', {}, cache)
          .then((response) => {
            _this.table.items = response.data
          })
          .finally(() => {
            _this.table.loading = false
          })
      },

      save () {
        const _this = this
        
        _this.form.loading = true

        _this.$http.patch('/harga/'+_this.form.input.id, _this.form.input)
          .then((/* response */) => {
            _this.alert('Berhasil mengubah data');
            Object.assign(_this.table.items[_this.table.index], _this.form.input)
            _this.close()
          })
          .finally(() => {
            _this.form.loading = false
          });
      },

      showForm () {
        const _this = this
        _this.dialog=true

        _this.$nextTick(function () {
          _this.$refs.normal.focus();
        });
      }
    },

    metaInfo: {
      title: 'Daftar Harga',
    }
  }
</script>