export const user = {
  namespaced: true,
  state: {
    id: '',
    logged_in: false,
    name: '',
    token: '',
    username: ''
  },
  getters: {
  },
  mutations: {
    CLEAR: (state) => {
      state.id = '';
      state.name = '';
      state.username = '';
      state.logged_in = false;
      state.token = '';
    },
    SET(state, payload) {
      state.id = payload.id;
      state.name = payload.name;
      state.username = payload.username;
      state.logged_in = payload.logged_in;
      state.token = payload.token;
    },
  },
  actions: {
    SET(context, payload) {
      context.commit('SET', payload);
    },
    CLEAR: context => new Promise((resolve) => {
      context.commit('CLEAR');
      resolve(true);
    })
  },
};
